import useDeepBookStore from '@/store/deepbook'
import useExplorer from '@cetus/hooks/src/useExplorer'
import { useAccountStore } from '@cetus/stores'
import { Icon, NoData, Table } from '@cetus/ui-kit'
import { addressAbridge, formatNumber } from '@cetus/utils'
import { HStack, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'
import CoinPairInfo from '../common/CoinPairInfo'
import TypeCon from '../common/proModeAndChart/ProModeTradeTab/TypeCon'

export default function TradeHistoryTableBlock() {
  const {
    deepBookOrderHistory,
    deepBookOrderHistoryLoading,
    currentDeepBookPool,
    isCheckedAllMarkets,
    deepBookPools,
    setShowDeepBookOrderHistoryNum
  } = useDeepBookStore()
  const { currentAccount, onWalletModal } = useAccountStore()

  const [type, setType] = useState('')
  const deepBookOrderHistoryList = useMemo(
    () => (isCheckedAllMarkets ? deepBookOrderHistory : deepBookOrderHistory?.filter((item: any) => item.poolId == currentDeepBookPool?.address)),
    [deepBookOrderHistory, currentDeepBookPool, isCheckedAllMarkets]
  )

  const dataSource = useMemo(() => {
    return deepBookOrderHistoryList.filter((item: any) => (!type ? true : type.split(',').includes(item.side)))
  }, [deepBookOrderHistoryList, type])

  useEffect(() => {
    setShowDeepBookOrderHistoryNum(dataSource?.length)
  }, [dataSource?.length])

  return (
    <Table
      dataSource={dataSource}
      columns={getColumns(type, setType)}
      loading={deepBookOrderHistoryLoading}
      // isFlexStart
      headBg="bg_secondary"
      tableContainerWrapStyle={{
        h: '100%'
      }}
      noData={
        !currentAccount?.address ? (
          <NoData type="nowallet" noBorder bg="none" onboard={() => onWalletModal(true)} />
        ) : dataSource?.length == 0 ? (
          <NoData type="nodata" text="No trade history" noBorder bg="none" />
        ) : undefined
      }
    />
  )
}

