import useDeepBookStore from '@/store/deepbook'
import { AddressCopyLink } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import { useAccountStore } from '@cetus/stores'
import { CheckBox } from '@cetus/ui-kit'
import { HStack, Modal, ModalBody, ModalCloseButton, ModalContent, ModalHeader, ModalOverlay, VStack } from '@chakra-ui/react'
import { useMemo } from 'react'

type ChangeAccountProps = {
  isOpen: boolean
  onClose: (isManualClose?: boolean) => void
}

export default function ChangeAccountModal({ isOpen, onClose }: ChangeAccountProps) {
  const { currentAccount } = useAccountStore()
  const { getExplorerUrl } = useExplorer()
  const { balanceManagerList, getCurrentBalanceManagerInfo, currentBalanceManagerInfoMap, setCurrentBalanceManagerInfo } = useDeepBookStore()

  const currentBalanceManagerInfo = useMemo(() => {
    if (currentAccount?.address) {
      return getCurrentBalanceManagerInfo(currentAccount?.address)
    }
    return null
  }, [currentAccount?.address, currentBalanceManagerInfoMap])

  return (
    <Modal autoFocus={false} returnFocusOnClose={false} trapFocus={false} isOpen={isOpen} onClose={() => onClose(false)} isCentered>
      <ModalOverlay />
      <ModalContent w="326px">
        <ModalHeader>Change Account</ModalHeader>
        <ModalCloseButton />
        <ModalBody textAlign="center" pb="20px">
          <VStack w="100%" gap="20px" align="flex-start">
            {balanceManagerList?.map((account: any) => {
              return (
                <HStack
                  key={account?.address}
                  sx={{ _hover: { p: { color: 'text_caption' } } }}
                  cursor="pointer"
                  onClick={() => {
                    setCurrentBalanceManagerInfo(currentAccount?.address, account)
                    onClose()
                  }}
                >
                  <CheckBox checked={currentBalanceManagerInfo?.balanceManager == account?.balanceManager} onClick={() => {}} />
                  <AddressCopyLink
                    subStringLengthStart={12}
                    subStringLengthEnd={12}
                    address={account?.balanceManager}
                    fontSize="14px"
                    isAddressCancelBubble={false}
                    hasUnderline={false}
                    color={currentBalanceManagerInfo?.balanceManager == account?.balanceManager ? 'text_caption' : 'text_paragraph'}
                    onAddressClick={() => setCurrentBalanceManagerInfo(currentAccount?.address, account)}
                    onClickLink={() => window.open(getExplorerUrl(account?.balanceManager, 'account'))}
                  />
                </HStack>
              )
            })}
          </VStack>
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}
