import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import { Decimal } from '@cetus/utils'
import { d } from '@cetusprotocol/deepbook-utils'
import useGetDeepBookOrderBook from './useGetDeepBookOrderBook'

export default function useGetDeepBookEstFee() {
  const { deepBookSDK } = usePeripherySDKStore()
  const { getRequestPool } = useGetDeepBookOrderBook()

  const getEstimatedMaxFee = async (poolInfo: any, quantityInput: string, priceInput: string, orderType: 'bid' | 'ask') => {
    if (d(poolInfo.takerFeeRate).lte(0)) return '0'
    const pool = { ...getRequestPool(poolInfo), taker_fee: poolInfo.takerFeeRate }
    const baseDecimals = poolInfo.baseAssets.decimals
    const quoteDecimals = poolInfo.quoteAssets.decimals
    const lotSize = d(poolInfo.lotSize).mul(10 ** baseDecimals)
    const quantity = d(quantityInput)
      .mul(10 ** baseDecimals)
      .div(lotSize)
      .toDP(0, Decimal.ROUND_HALF_UP)
      .mul(lotSize)
      .toString()
    const price = d(priceInput)
      .mul(10 ** (quoteDecimals - baseDecimals + 9))
      .toString()

    console.log('getEstimatedMaxFee params: ', { pool, quantity, price })
    const res = await deepBookSDK.DeepbookUtils.estimatedMaxFee(pool, quantity, price, false, orderType == 'bid', false)
    console.log(' ~ useGetDeepBookEstFee.ts:29 ~ getEstimatedMaxFee ~ res:', res)
    const feeCoinDecimals = orderType == 'bid' ? poolInfo.quoteAssets.decimals : poolInfo.baseAssets.decimals
    return res?.quantity ? d(res?.quantity).div(Math.pow(10, feeCoinDecimals)).toString() : '0'
  }

  return { getEstimatedMaxFee }
}
