import useGetDeepBookManagerBalance from '@/hooks/deepbook/useGetDeepBookManagerBalance'
import useDeepBookStore from '@/store/deepbook'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { Icon, RefreshButton } from '@cetus/ui-kit'
import { addressAbridge } from '@cetus/utils'
import { Box, HStack, SkeletonCircle, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'
import AssetsInfoModal from './AssetsInfoModal'
import ChangeAccountModal from './ChangeAccountModal'

import useDeepBookAssetsActions from '@/hooks/deepbook/useDeepBookAssetsActions'
import useGetDeepBookBalance from '@/hooks/deepbook/useGetDeepBookBalance'
import { CetusTooltip } from '@cetus/design'
import { useAccountBalance } from '@cetus/hooks'
import useDocumentSize from '@cetus/hooks/src/useDocumentSize'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { HTextLabelBox, SingleCoinImage } from '@cetus/ui-kit'
import { d, formatNumberWithDown } from '@cetus/utils'
import { Button, Skeleton } from '@chakra-ui/react'

interface TokenAssetItemProps {
  token: Token
  onDeposit: (token: Token) => void
  onWithdraw: (token: Token) => void
  freeBalance: string
  balanceInfo: any
  lockBalance: string
}

function TokenAssetItem({ token, onDeposit, onWithdraw, freeBalance, balanceInfo, lockBalance }: TokenAssetItemProps) {
  const isLoading = !token?.symbol
  return (
    <VStack align="flex-start" w="100%" height="160px" gap="12px" mt="12px">
      {isLoading ? (
        <HStack w="100%">
          <SkeletonCircle />
          <Skeleton />
        </HStack>
      ) : (
        <HStack w="100%">
          <SingleCoinImage imageUrl={token?.logo_url} imgBoxStyle={{ w: '24px', h: '24px' }} imageStyle={{ w: '24px', h: '24px' }} />
          <Text color="text_caption">{token?.symbol}</Text>
        </HStack>
      )}

      <HStack w="100%" h="20px" justify="space-between">
        <HStack gap="4px">
          <Text>Locked</Text>
          <CetusTooltip tooltip={<Text fontSize="12px">Locked in Limit Orders</Text>} placement="top">
            <Icon xlinkHref="#icon-icon_tips" svgW="20px" svgH="20px" />
          </CetusTooltip>
        </HStack>
        {isLoading ? <Skeleton /> : <Text color="text_caption">{lockBalance}</Text>}
      </HStack>

      <HTextLabelBox
        isLoading={false}
        label="Free Balance"
        value={isLoading ? <Skeleton /> : (freeBalance ?? '0.00')}
        labelStyle={{ fontSize: '14px', h: '20px', lineHeight: '20px' }}
        valueStyle={{ fontSize: '14px', h: '20px', lineHeight: '20px' }}
        skeletonStyle={{ valueW: '96px', valueH: '16px' }}
      />

      <HTextLabelBox
        isLoading={false}
        label="Wallet Balance"
        value={isLoading ? <Skeleton /> : formatNumberWithDown(balanceInfo?.balanceFormat ?? 0)}
        labelStyle={{ fontSize: '14px', h: '20px', lineHeight: '20px' }}
        valueStyle={{ fontSize: '14px', h: '20px', lineHeight: '20px' }}
        skeletonStyle={{ valueW: '96px', valueH: '16px' }}
      />

      <HStack w="100%">
        <Button
          variant="outline"
          h="28px"
          w="50%"
          borderRadius="8px"
          isDisabled={d(balanceInfo?.balanceFormat ?? 0).lte(0)}
          fontSize="12px"
          onClick={() => onDeposit(token)}
        >
          Deposit
        </Button>
        <Button
          variant="ghost"
          h="28px"
          w="50%"
          borderRadius="8px"
          isDisabled={d(freeBalance ?? 0).lte(0)}
          fontSize="12px"
          borderColor="transparent !important"
          onClick={() => onWithdraw(token)}
        >
          Withdraw
        </Button>
      </HStack>
    </VStack>
  )
}

function AssetsInfo() {
  const { currentDeepBookPool, currentBalanceManagerInfoMap, deepBookOpenOrders } = useDeepBookStore()
  const { currentAccount } = useAccountStore()
  const [isOpenModal, setIsOpenModal] = useState(false)
  const [isOpenAccountModal, setIsOpenAccountModal] = useState(false)
  const [modalTitle, setModalTitle] = useState<'Deposit' | 'Withdraw'>('Deposit')
  const [tokenInfo, setTokenInfo] = useState<any>()
  const { getCurrentBalanceManagerInfo, balanceManagerList } = useDeepBookStore()

  // const currentBalanceManagerInfo = useMemo(() => {
  //   if (currentAccount?.address) {
  //     return getCurrentBalanceManagerInfo(currentAccount?.address)
  //   }
  //   return null
  // }, [currentAccount?.address, currentBalanceManagerInfoMap])

  const currentBalanceManagerInfo = useDeepBookStore((state: any) => {
    const address = currentAccount?.address
    if (!address) return null
    return state.currentBalanceManagerInfoMap[address] || null
  })
  const { getManagerBalance } = useGetDeepBookManagerBalance()
  const { deposit, withdraw } = useDeepBookAssetsActions()

  useEffect(() => {
    if (currentBalanceManagerInfo?.balanceManager && currentDeepBookPool?.address && currentAccount?.address) {
      getManagerBalance(
        [
          { coin_type: currentDeepBookPool?.baseAssets.coin_type, decimals: currentDeepBookPool?.baseAssets.decimals },
          { coin_type: currentDeepBookPool?.quoteAssets.coin_type, decimals: currentDeepBookPool?.quoteAssets.decimals }
        ],
        currentAccount?.address,
        currentBalanceManagerInfo?.balanceManager
      )
    }
  }, [currentBalanceManagerInfo, currentAccount?.address, currentDeepBookPool])

  const { baseFreeBalance, quoteFreeBalance, baseBalance, quoteBalance } = useGetDeepBookBalance()

  const lockInfo = useMemo(() => {
    const result = {
      baseLock: '0',
      quoteLock: '0'
    }
    if (deepBookOpenOrders?.length > 0) {
      let baseLock = '0'
      let quoteLock = '0'
      deepBookOpenOrders.forEach((item: any) => {
        if (currentDeepBookPool?.address === item?.address) {
          if (item?.side == 'Buy') {
            quoteLock = d(quoteLock)
              .add(d(item.originalQuantity).sub(d(item.filledQuantity)).mul(item.price))
              .toString()
          } else {
            baseLock = d(baseLock)
              .add(d(item.originalQuantity).sub(d(item.filledQuantity)))
              .toString()
          }
        }
      })

      result['baseLock'] = baseLock
      result['quoteLock'] = quoteLock
    }

    return result
  }, [deepBookOpenOrders, currentDeepBookPool])

  const handleDeposit = (token: Token) => {
    setTokenInfo(token)
    setModalTitle('Deposit')
    setIsOpenModal(true)
  }

  const handleWithdraw = (token: Token) => {
    setTokenInfo(token)
    setModalTitle('Withdraw')
    setIsOpenModal(true)
  }

  const { fetchAccountBalance } = useAccountBalance()

  const handleRefresh = () => {
    if (currentDeepBookPool?.address && currentAccount?.address) {
      getManagerBalance(
        [
          { coin_type: currentDeepBookPool?.baseAssets.coin_type, decimals: currentDeepBookPool?.baseAssets.decimals },
          { coin_type: currentDeepBookPool?.quoteAssets.coin_type, decimals: currentDeepBookPool?.quoteAssets.decimals }
        ],
        currentAccount?.address,
        currentBalanceManagerInfo?.balanceManager
      )
      fetchAccountBalance()
    }
  }

  const { size } = useDocumentSize()
  const { isApp } = useWindowWidth()

  return (
    <VStack w="100%" align="flex-start" bg="bg_secondary" borderRadius="12px" h="100%">
      <Box w="100%" p="16px 16px 0">
        <HStack w="100%" borderBottom="1px solid" borderColor="border" pb="12px">
          <HStack w="100%">
            <Text fontSize="16px" color="text_caption">
              Asset Info
            </Text>
            {isApp && <RefreshButton handleRefresh={handleRefresh} w="28px" h="28px" minW="28px" innerStyle={{ bg: 'none' }} />}
          </HStack>
          {balanceManagerList?.length > 1 && (
            <VStack p="6px 8px 8px" bg="primary_opacity.10" borderRadius="8px" justify="center">
              <HStack
                w="auto"
                gap="0px"
                cursor="pointer"
                align="flex-end"
                onClick={() => setIsOpenAccountModal(true)}
                _hover={{ svg: { fill: 'text_caption' } }}
                lineHeight="40px"
              >
                <Text lineHeight="1" color="primary">
                  {addressAbridge(currentBalanceManagerInfo?.balanceManager)}
                </Text>
                <Icon xlinkHref="#icon-icon_descending_nor" svgW="20px" svgH="20px" mb="-3px" transform={'rotate(-90deg)'} />
              </HStack>
            </VStack>
          )}
        </HStack>
      </Box>
      <VStack
        w="100%"
        align="flex-start"
        p="0 16px 16px"
        minHeight="334px"
        h={{ base: '100%', lg: size?.h - 80 - 430 - 8 - 46 - 40 }}
        overflow="auto"
        bg="bg_secondary"
      >
        <TokenAssetItem
          token={currentDeepBookPool?.baseAssets}
          onDeposit={handleDeposit}
          onWithdraw={handleWithdraw}
          depositDisabled={d(baseBalance?.balanceFormat ?? 0).lte(0)}
          withdrawDisabled={d(baseFreeBalance ?? 0).lte(0)}
          freeBalance={baseFreeBalance}
          balanceInfo={baseBalance}
          lockBalance={lockInfo?.baseLock}
        />
        <TokenAssetItem
          token={currentDeepBookPool?.quoteAssets}
          onDeposit={handleDeposit}
          onWithdraw={handleWithdraw}
          depositDisabled={d(quoteBalance?.balanceFormat ?? 0).lte(0)}
          withdrawDisabled={d(quoteFreeBalance ?? 0).lte(0)}
          balanceInfo={quoteBalance}
          freeBalance={quoteFreeBalance}
          lockBalance={lockInfo?.quoteLock}
        />
        {isOpenModal && (
          <AssetsInfoModal
            token={tokenInfo}
            title={modalTitle}
            isOpen={isOpenModal}
            isBaseAsset={tokenInfo?.symbol === currentDeepBookPool?.baseAssets?.symbol}
            onClose={() => setIsOpenModal(false)}
            onConfirm={inputValue => {
              if (modalTitle === 'Deposit') {
                deposit(inputValue, tokenInfo?.symbol === currentDeepBookPool?.baseAssets?.symbol).then(() => {
                  setIsOpenModal(false)
                })
              } else {
                withdraw(inputValue, tokenInfo?.symbol === currentDeepBookPool?.baseAssets?.symbol).then(() => {
                  setIsOpenModal(false)
                })
              }
            }}
          />
        )}
        {isOpenAccountModal && <ChangeAccountModal isOpen={isOpenAccountModal} onClose={() => setIsOpenAccountModal(false)} />}
      </VStack>
    </VStack>
  )
}

export default AssetsInfo
