import useDeepBookStore from '@/store/deepbook'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'

export type OrderType = 'bid' | 'ask' | 'all'

export default function useGetDeepBookOrderBook() {
  const { deepBookSDK } = usePeripherySDKStore()
  const { currentDeepBookPool, setDeepBookAskList, setDeepBookBidList, setDeepBookOrderBookLoading, deepBookPrice } = useDeepBookStore()

  //  deepbook pool 
  const getRequestPool = (poolInfo: any) => ({
    address: poolInfo?.address,
    baseCoin: {
      coinType: poolInfo?.baseAssets?.coin_type,
      decimals: poolInfo?.baseAssets?.decimals
    },
    quoteCoin: {
      coinType: poolInfo?.quoteAssets?.coin_type,
      decimals: poolInfo?.quoteAssets?.decimals
    }
  })

  const enhanceOrders = (list: any[]) =>
    list.map(item => ({
      ...item,
      total: item.price * item.quantity
    }))

  const getOrderBook = async (orderType: OrderType, tickSize: string, isRefresh: boolean = false, length: number = 9) => {
    try {
      if (!currentDeepBookPool?.price) return
      if (isRefresh) setDeepBookOrderBookLoading(true)

      const pool = getRequestPool(currentDeepBookPool)
      const unit = tickSize.split('.')[1] ? tickSize.split('.')[1].length : 0

      const priceIntW = currentDeepBookPool?.price?.split('.')?.[0]?.length || 0
      const priceInt = unit < priceIntW ? priceIntW + 1 : unit

      const res = await deepBookSDK.DeepbookUtils.getOrderBook(pool, orderType, priceInt)
      console.log(' ~ useGetDeepBookOrderBook.ts:41 ~ getOrderBook ~ res:', res)
      const askList = enhanceOrders(JSON.parse(JSON.stringify(res.ask)))
      const bidList = enhanceOrders(JSON.parse(JSON.stringify(res.bid)))
      if (orderType === 'ask' || orderType === 'all') {
        setDeepBookAskList(askList)
      }

      if (orderType === 'bid' || orderType === 'all') {
        setDeepBookBidList(bidList)
      }
      setDeepBookOrderBookLoading(false)
    } catch (error) {
      console.error('getOrderBook error:', error)
    }
  }

  return { getOrderBook, getRequestPool }
}
