import AssetsInfo from '@/components/deepbook/AssetsInfo'
import DeepbookChart from '@/components/deepbook/DeepbookChart'
import FooterBlock from '@/components/deepbook/FooterBlock'
import OrderBookBlock from '@/components/deepbook/OrderBookBlock'
import TopDataBlock from '@/components/deepbook/TopDataBlock'
import TradeBlock from '@/components/deepbook/TradeBlock'
import TradeTableBlock from '@/components/deepbook/TradeTableBlock'
import { SelectTab } from '@cetus/design'
import { HStack, VStack } from '@chakra-ui/react'
import { useState } from 'react'

interface DeepBookH5Props {
  currentDeepBookPool: any
  deepBookPools: any[]
  currentAccount: any
  currentBalanceManagerInfo: any
}

function DeepBookH5({ currentDeepBookPool, deepBookPools, currentAccount, currentBalanceManagerInfo }: DeepBookH5Props) {
  // ====================  ====================
  const [currentTab, setCurrentTab] = useState<any>({
    label: 'Chart',
    value: 'chart'
  })
  const [isOpenCard, setIsOpenCard] = useState(true)
  const [currentH5Menu, setCurrentH5Menu] = useState<any>({
    label: 'Markets',
    value: 'Markets'
  })

  // ====================  ====================
  const tabList = [
    { label: 'Chart', value: 'chart' },
    { label: 'Orderbook', value: 'orderBook' }
  ]

  const h5MenuList = [
    { label: 'Markets', value: 'Markets' },
    { label: 'Trade', value: 'trade' },
    { label: 'Account', value: 'account' }
  ]

  // ====================  ====================
  const handleChangeTab = (tab: any) => {
    setCurrentTab(tab)
  }

  return (
    <VStack w="100%" paddingBottom="68px">
      <TopDataBlock isOpenCard={isOpenCard} setIsOpenCard={setIsOpenCard} />

      {currentH5Menu.value === 'Markets' && (
        <VStack bg="bg_secondary" w="100%" h="516px" gap="0">
          <HStack padding="16px" w="100%">
            <SelectTab
              type="outlineTab"
              tabList={tabList}
              currentTab={currentTab?.label}
              handleChangeTab={handleChangeTab}
              wrapStyle={{
                w: '100%',
                h: '40px',
                p: '4px',
                borderRadius: '8px'
              }}
              itemStyle={{
                flex: {
                  lg: '1',
                  base: 'auto'
                },
                fontSize: '14px',
                margin: '0px',
                borderRadius: '4px'
              }}
            />
          </HStack>
          {currentTab.value === 'chart' && <DeepbookChart currentDeepBookPool={currentDeepBookPool} />}
          {currentTab.value === 'orderBook' && <OrderBookBlock isOpenCard={isOpenCard} />}
        </VStack>
      )}

      {currentH5Menu.value === 'trade' && (
        <>
          <TradeBlock />
          <TradeTableBlock />
        </>
      )}

      {currentH5Menu.value === 'account' && <AssetsInfo />}

      <FooterBlock />

      <SelectTab
        type="borderTab"
        tabList={h5MenuList}
        currentTab={currentH5Menu?.label}
        handleChangeTab={setCurrentH5Menu}
        wrapStyle={{
          w: '100%',
          h: '60px',
          minH: '60px',
          borderRadius: '0px',
          bg: 'bg_secondary',
          border: 'none',
          position: 'fixed',
          bottom: '0px',
          left: '0px',
          zIndex: '1000'
        }}
        itemStyle={{
          flex: {
            lg: '1',
            base: 'auto'
          },
          fontSize: '14px',
          margin: '0px',
          borderRadius: '4px'
        }}
      />
    </VStack>
  )
}

export default DeepBookH5
