import { create } from 'zustand'
import { persist } from 'zustand/middleware'

export type DeepbookPrice = {
  price: string
  poolId: string
}

interface DeepBookState {
  deepBookPools: any[]
  setDeepBookPools: (deepBookPools: any[]) => void

  deepBookPoolsObj: Record<string, any>
  setDeepBookPoolsObj: (deepBookPoolsObj: Record<string, any>) => void

  deepBookPoolLoading: boolean
  setDeepBookPoolLoading: (deepBookPoolLoading: boolean) => void

  currentDeepBookPool: any
  setCurrentDeepBookPool: (currentDeepBookPool: any) => void

  deepBookOrderBookLoading: boolean
  setDeepBookOrderBookLoading: (deepBookOrderBookLoading: boolean) => void

  deepBookAskList: any[]
  setDeepBookAskList: (deepBookAskList: any[]) => void

  deepBookBidList: any[]
  setDeepBookBidList: (deepBookBidList: any[]) => void

  queryDeepBookPoolLoading: boolean
  setQueryDeepBookPoolLoading: (queryDeepBookPoolLoading: boolean) => void

  queryDeepBookPools: any[]
  setQueryDeepBookPools: (queryDeepBookPools: any[]) => void

  // key
  currentBalanceManagerInfoMap: Record<string, any>
  setCurrentBalanceManagerInfo: (walletAddress: string, currentBalanceManagerInfo: any) => void
  getCurrentBalanceManagerInfo: (walletAddress: string) => any

  balanceManagerList: any[]
  setBalanceManagerList: (balanceManagerList: any[]) => void

  managerBalanceObjs: any[]
  setManagerBalanceObjs: (managerBalanceObjs: any[]) => void

  deepBookOpenOrders: any[]
  setDeepBookOpenOrders: (deepBookOpenOrders: any[]) => void

  cancelOrderLoading: boolean
  setCancelOrderLoading: (cancelOrderLoading: boolean) => void

  orderListLoading: boolean
  setOrderListLoading: (orderListLoading: boolean) => void

  isCheckedAllMarkets: boolean
  setIsCheckedAllMarkets: (isCheckedAllMarkets: boolean) => void

  deepBookOrderHistory: any[]
  setDeepBookOrderHistory: (deepBookOrderHistory: any[]) => void

  showDeepBookOrderHistoryNum: number
  setShowDeepBookOrderHistoryNum: (showDeepBookOrderHistoryNum: number) => void

  deepBookOrderHistoryLoading: boolean
  setDeepBookOrderHistoryLoading: (deepBookOrderHistoryLoading: boolean) => void

  deepBookSettleList: any[]
  setDeepBookSettleList: (deepBookSettleList: any[]) => void

  deepBookSettleListLoading: boolean
  setDeepBookSettleListLoading: (deepBookSettleListLoading: boolean) => void

  claimSettleLoading: boolean
  setClaimSettleLoading: (claimSettleLoading: boolean) => void

  depositAssetsLoading: boolean
  setDepositAssetsLoading: (depositAssetsLoading: boolean) => void

  withdrawAssetsLoading: boolean
  setWithdrawAssetsLoading: (withdrawAssetsLoading: boolean) => void

  localDeepBookPools: any[]
  setLocalDeepBookPools: (localDeepBookPools: any[]) => void

  placeOrderPrice: string
  setPlaceOrderPrice: (placeOrderPrice: string) => void

  orderType: 'Market' | 'Limit'
  setOrderType: (orderType: 'Market' | 'Limit') => void

  deepbookPrice: DeepbookPrice | undefined
  setDeepbookPrice: (value: DeepbookPrice) => void
}

const store = (set: any, get: any) => ({
  deepBookPools: [],
  setDeepBookPools: deepBookPools => set({ deepBookPools }),

  deepBookPoolsObj: {},
  setDeepBookPoolsObj: deepBookPoolsObj => set({ deepBookPoolsObj }),

  deepBookPoolLoading: false,
  setDeepBookPoolLoading: deepBookPoolLoading => set({ deepBookPoolLoading }),

  currentDeepBookPool: {} as any,
  setCurrentDeepBookPool: currentDeepBookPool => set({ currentDeepBookPool }),

  deepBookOrderBookLoading: true,
  setDeepBookOrderBookLoading: deepBookOrderBookLoading => set({ deepBookOrderBookLoading }),

  deepBookAskList: [],
  setDeepBookAskList: deepBookAskList => set({ deepBookAskList }),

  deepBookBidList: [],
  setDeepBookBidList: deepBookBidList => set({ deepBookBidList }),

  queryDeepBookPoolLoading: false,
  setQueryDeepBookPoolLoading: queryDeepBookPoolLoading => set({ queryDeepBookPoolLoading }),

  queryDeepBookPools: [],
  setQueryDeepBookPools: queryDeepBookPools => set({ queryDeepBookPools }),

  // key
  currentBalanceManagerInfoMap: {},
  setCurrentBalanceManagerInfo: (walletAddress: string, currentBalanceManagerInfo: any) =>
    set(state => ({
      currentBalanceManagerInfoMap: {
        ...state.currentBalanceManagerInfoMap,
        [walletAddress]: currentBalanceManagerInfo
      }
    })),
  getCurrentBalanceManagerInfo: (walletAddress: string) => {
    const state = get()
    return state.currentBalanceManagerInfoMap[walletAddress] || null
  },

  balanceManagerList: [],
  setBalanceManagerList: (balanceManagerList: any[]) => set({ balanceManagerList }),

  managerBalanceObjs: [] as any,
  setManagerBalanceObjs: (managerBalanceObjs: any[]) => set({ managerBalanceObjs }),

  deepBookOpenOrders: [],
  setDeepBookOpenOrders: deepBookOpenOrders => set({ deepBookOpenOrders }),

  showOpenOrdersNum: 0,
  setShowOpenOrdersNum: showOpenOrdersNum => set({ showOpenOrdersNum }),

  cancelOrderLoading: false,
  setCancelOrderLoading: cancelOrderLoading => set({ cancelOrderLoading }),

  orderListLoading: false,
  setOrderListLoading: orderListLoading => set({ orderListLoading }),

  isCheckedAllMarkets: false,
  setIsCheckedAllMarkets: isCheckedAllMarkets => set({ isCheckedAllMarkets }),

  deepBookOrderHistory: [],
  setDeepBookOrderHistory: deepBookOrderHistory =>
    set({
      deepBookOrderHistory,
      showDeepBookOrderHistoryNum: deepBookOrderHistory?.filter(
        (item: any) => item?.poolId === get().currentDeepBookPool?.address || get().isCheckedAllMarkets
      )?.length
    }),

  deepBookOrderHistoryLoading: true,
  setDeepBookOrderHistoryLoading: deepBookOrderHistoryLoading => set({ deepBookOrderHistoryLoading }),

  showDeepBookOrderHistoryNum: 0,
  setShowDeepBookOrderHistoryNum: showDeepBookOrderHistoryNum => set({ showDeepBookOrderHistoryNum }),

  deepBookSettleList: [],
  setDeepBookSettleList: deepBookSettleList => set({ deepBookSettleList }),

  deepBookSettleListLoading: false,
  setDeepBookSettleListLoading: deepBookSettleListLoading => set({ deepBookSettleListLoading }),

  claimSettleLoading: false,
  setClaimSettleLoading: claimSettleLoading => set({ claimSettleLoading }),

  depositAssetsLoading: false,
  setDepositAssetsLoading: depositAssetsLoading => set({ depositAssetsLoading }),

  withdrawAssetsLoading: false,
  setWithdrawAssetsLoading: withdrawAssetsLoading => set({ withdrawAssetsLoading }),

  localDeepBookPools: [],
  setLocalDeepBookPools: localDeepBookPools => set({ localDeepBookPools }),

  placeOrderPrice: '',
  setPlaceOrderPrice: placeOrderPrice => set({ placeOrderPrice }),

  orderType: 'Market',
  setOrderType: orderType => set({ orderType }),

  deepbookPrice: undefined,
  setDeepbookPrice: (value: DeepbookPrice) => {
    set(() => ({
      deepbookPrice: value
    }))
  }
})

const useDeepBookStore = create(
  persist(store, {
    name: 'useDeepBookStore',
    partialize: state => {
      const { currentBalanceManagerInfoMap, localDeepBookPools } = state
      return { currentBalanceManagerInfoMap, localDeepBookPools }
    },
    version: 1
  })
)

export default useDeepBookStore
