import { NumericFormatInput } from '@cetus/ui-kit'
import { HStack, Input, Text, VStack } from '@chakra-ui/react'
import { useRef, useState } from 'react'
import { InputBlockProps } from './InputBlockGroup'

export default function InputBlock(props: InputBlockProps) {
  const { title, symbol, value, onChange, isMarket, decimals } = props
  const fromRef = useRef<HTMLInputElement | null>(null)
  const [focused, setFocused] = useState<boolean>(false)

  return (
    <VStack
      w="100%"
      ref={fromRef}
      p="12px"
      border="1px solid"
      borderRadius="12px"
      bg="bg_secondary"
      alignItems="flex-start"
      gap="0px"
      position="relative"
      zIndex="5"
      onFocus={() => {
        setFocused(true)
      }}
      onBlur={() => {
        setFocused(false)
      }}
      borderColor={focused ? 'token_active_border' : 'border'}
      boxShadow={focused ? '0px 0px 6px 0px #0067AD' : ''}
    >
      <Text fontSize="12px" fontWeight="500">
        {title}
      </Text>
      <HStack w="100%" alignItems="flex-start" pt="8px">
        {isMarket ? (
          <Input value="Market" disabled placeholder="Market" color="text_caption !important" fontSize="18px" fontWeight="500" h="18px" />
        ) : (
          <NumericFormatInput
            value={value}
            onChange={onChange}
            decimals={decimals}
            placeholder="0.0"
            style={{
              width: 'calc(100% - 8px)',
              background: 'none',
              whiteSpace: 'nowrap',
              opacity: 1,
              outline: 'none',
              color: 'var(--chakra-colors-text_caption)',
              fontSize: '18px',
              height: '18px',
              lineHeight: '18px',
              touchAction: 'manipulation',
              transition: 'all 0.3s',
              fontWeight: '500'
            }}
          />
        )}

        <Text mt="-8px" fontWeight="500">
          {symbol}
        </Text>
      </HStack>
    </VStack>
  )
}
