import useGetDeepBookOrderBook, { OrderType } from '@/hooks/deepbook/useGetDeepBookOrderBook'
import { Icon } from '@cetus/ui-kit'
import { HStack, Menu, MenuButton, MenuItem, MenuList, Text } from '@chakra-ui/react'
import { useEffect, useRef, useState } from 'react'

const TickSizeSelect = ({
  orderBookTab,
  tickSizeList,
  defaultTickSize,
  setDefaultTickSize
}: {
  orderBookTab: OrderType
  tickSizeList: string[]
  defaultTickSize: string
  setDefaultTickSize: (tickSize: string) => void
}) => {
  const { getOrderBook } = useGetDeepBookOrderBook()
  const [isOpen, setIsOpen] = useState(false)
  const [buttonWidth, setButtonWidth] = useState<number>(0)
  const buttonRef = useRef<HTMLDivElement>(null)

  const selectTickSize = (tickSize: string) => {
    setDefaultTickSize(tickSize)
    getOrderBook(orderBookTab, tickSize, false, 9)
  }

  // 
  useEffect(() => {
    if (buttonRef.current) {
      setButtonWidth(buttonRef.current.offsetWidth)
    }
  }, [defaultTickSize])

  return (
    <Menu onOpen={() => setIsOpen(true)} onClose={() => setIsOpen(false)}>
      <MenuButton>
        <HStack
          ref={buttonRef}
          border="1px solid"
          borderRadius="8px"
          p="6px 8px"
          cursor="pointer"
          minW="40px"
          w="auto"
          h="24px"
          justifyContent="space-between"
          borderColor="border"
        >
          <Text color={isOpen ? 'text_caption' : 'text_paragraph'}>{defaultTickSize}</Text>
          <Icon
            xlinkHref="#icon-icon_arrow"
            boxW="12px"
            boxH="12px"
            transform={isOpen ? 'rotate(180deg)' : 'rotate(0deg)'}
            transition="transform 0.2s ease-in-out"
            svgFill={isOpen ? 'text_caption' : 'text_paragraph'}
          />
        </HStack>
      </MenuButton>
      <MenuList
        p="0"
        minW="unset"
        zIndex={99999}
        borderRadius="8px"
        border="1px solid"
        borderColor="border"
        w={`${buttonWidth}px`} // 
      >
        {tickSizeList.map(item => (
          <MenuItem
            key={item}
            onClick={() => selectTickSize(item)}
            p="8px"
            bg="none"
            justifyContent="flex-start"
            cursor="pointer"
            _hover={{ color: 'text_caption' }}
          >
            {item}
          </MenuItem>
        ))}
      </MenuList>
    </Menu>
  )
}

export default TickSizeSelect
