import useDeepBookStore from '@/store/deepbook'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { d, formatNumber, formatNumberWithKMB } from '@cetus/utils'
import { Box, HStack, Skeleton, Text, VStack } from '@chakra-ui/react'
import { useCallback, useEffect, useMemo, useRef, useState } from 'react'

const OrderBookTable = ({ orderBookTab, tickSize, visibleRows }: { orderBookTab: string; tickSize: string; visibleRows: number }) => {
  const { deepBookAskList, deepBookBidList, deepBookOrderBookLoading, currentDeepBookPool, setPlaceOrderPrice, orderType, deepbookPrice } =
    useDeepBookStore()
  const [changedOrders, setChangedOrders] = useState<Set<string>>(new Set())
  const { isApp } = useWindowWidth()

  //  useRef 
  const prevDataRef = useRef<{ asks: any[]; bids: any[] }>({ asks: [], bids: [] })
  const isFirstRenderRef = useRef(true)

  // 
  const orderBookData = useMemo(
    () => ({
      asks: deepBookAskList.sort((a, b) => parseFloat(b.price) - parseFloat(a.price)).reverse() || [],
      bids: deepBookBidList.sort((a, b) => parseFloat(b.price) - parseFloat(a.price)) || []
    }),
    [deepBookAskList, deepBookBidList]
  )

  //  - 
  useEffect(() => {
    if (orderBookData.asks.length === 0 && orderBookData.bids.length === 0) {
      return
    }
    if (isFirstRenderRef.current) {
      // 
      prevDataRef.current = orderBookData
      isFirstRenderRef.current = false
      return
    }

    const changed = new Set<string>()

    // 
    const createDataHash = (order: any) => {
      return `${order.price}-${order.total}-${order.quantity}`
    }

    // 
    const createHashMap = (orders: any[]) => {
      const hashMap = new Map<string, any>()
      orders.forEach(order => {
        const hash = createDataHash(order)
        hashMap.set(hash, order)
      })
      return hashMap
    }

    // 
    const currentAsksHashMap = createHashMap(orderBookData.asks)
    const currentBidsHashMap = createHashMap(orderBookData.bids)
    const prevAsksHashMap = createHashMap(prevDataRef.current.asks)
    const prevBidsHashMap = createHashMap(prevDataRef.current.bids)

    //  asks 
    currentAsksHashMap.forEach((currentAsk, hash) => {
      const prevAsk = prevAsksHashMap.get(hash)
      if (!prevAsk) {
        // 
        changed.add(`ask-${hash}`)
      }
    })

    //  bids 
    currentBidsHashMap.forEach((currentBid, hash) => {
      const prevBid = prevBidsHashMap.get(hash)
      if (!prevBid) {
        // 
        changed.add(`bid-${hash}`)
      }
    })

    if (changed.size > 0) {
      setChangedOrders(changed)
      const timer = setTimeout(() => {
        setChangedOrders(new Set())
      }, 1000)
      prevDataRef.current = orderBookData
      return () => clearTimeout(timer)
    }
  }, [orderBookData])

  useEffect(() => {
    if (currentDeepBookPool?.address) {
      prevDataRef.current = { asks: [], bids: [] }
      isFirstRenderRef.current = true
    }
  }, [currentDeepBookPool?.address])

  //  - 
  const asksToShow = useMemo(() => orderBookData.asks.slice(0, visibleRows).reverse(), [orderBookData.asks, visibleRows])
  const bidsToShow = useMemo(() => orderBookData.bids.slice(0, visibleRows), [orderBookData.bids, visibleRows])

  // 
  const maxTotal = useMemo(() => {
    const askMax = asksToShow.length > 0 ? Math.max(...asksToShow.map(ask => ask.total || 0)) : 0
    const bidMax = bidsToShow.length > 0 ? Math.max(...bidsToShow.map(bid => bid.total || 0)) : 0
    return Math.max(askMax, bidMax)
  }, [asksToShow, bidsToShow])

  //  -  useCallback 
  const calculateDepthPercentage = useCallback((total: number, maxTotal: number) => {
    if (maxTotal === 0) return 0
    return (total / maxTotal) * 100
  }, [])

  //  Spread
  const spread = useMemo(() => {
    if (deepbookPrice && deepbookPrice?.price && deepbookPrice?.poolId == currentDeepBookPool?.address) {
      return deepbookPrice?.price
    }
    return ''
  }, [currentDeepBookPool?.address, deepbookPrice])

  //  renderOrderRow - 
  const renderOrderRow = useCallback(
    (order: any, isAsk: boolean, index: number, isApp: boolean) => {
      const depthPercentage = calculateDepthPercentage(order.total || 0, maxTotal)
      const depthColor = isAsk ? 'rgba(255, 80, 116, 0.1)' : 'rgba(103, 255, 216, 0.1)'
      const priceColor = isAsk ? '#FF5074' : '#67FFD8'

      // 
      const createDataHash = (order: any) => {
        return `${order.price}-${order.total}-${order.quantity}`
      }
      const dataHash = createDataHash(order)
      const orderKey = `${isAsk ? 'ask' : 'bid'}-${dataHash}`
      const isChanged = changedOrders.has(orderKey)

      const getTickSizeNumber = (price: string, tickSize: string) => {
        const priceDecimal = d(price)
        const tickSizeDecimal = d(tickSize)
        const remainder = priceDecimal.mod(tickSizeDecimal)
        return priceDecimal.sub(remainder).toString()
      }

      return (
        <HStack
          as="div"
          role="group"
          key={`${orderKey}-${order.size}-${order.total}`}
          w="100%"
          h="20px"
          cursor="pointer"
          position="relative"
          spacing={0}
          justify="space-between"
          p="0 1px"
          transition="all 0.3s ease-in-out"
          _hover={{ bg: 'rgba(118,200,255,0.05)' }}
          onClick={() => (orderType == 'Limit' ? setPlaceOrderPrice(order.price) : null)}
        >
          {/*  */}
          <Box
            position="absolute"
            top="0"
            bottom="0"
            h="20px"
            bg={depthColor}
            width={`calc(${depthPercentage}% - 2px)`}
            zIndex="0"
            transition="all 0.3s ease-in-out"
            _groupHover={{
              bg: isAsk ? 'rgba(255, 80, 116, 0.2)' : 'rgba(103, 255, 216, 0.2)'
            }}
            // H5
            left={isApp && !isAsk ? 'auto' : '1px'}
            right={isApp && !isAsk ? '1px' : 'auto'}
          />

          {/*  */}
          {isChanged && (
            <Box
              position="absolute"
              top="0"
              bottom="0"
              h="20px"
              bg={isAsk ? 'rgba(255, 80, 116, 0.2)' : 'rgba(103, 255, 216, 0.2)'}
              zIndex="0"
              animation="slideIn 0.5s ease-out"
              // H5
              left={isApp && !isAsk ? 'auto' : '1px'}
              right={isApp && !isAsk ? '1px' : 'auto'}
            />
          )}

          {/* appSizePrice */}
          {isApp && !isAsk ? (
            <>
              {/* Size */}
              <Text
                position="relative"
                zIndex="1"
                fontSize="12px"
                color="text_caption"
                textAlign="left"
                flex="1"
                pl={isAsk ? '0px' : '16px'}
                transition="all 0.3s ease-in-out"
              >
                {formatNumber(order.quantity || 0, 2)}
              </Text>

              {/* Price */}
              <Text
                position="relative"
                zIndex="1"
                fontSize="12px"
                fontWeight="500"
                color={priceColor}
                textAlign="right"
                flex="1"
                pr={isAsk ? '16px' : '0px'}
                transition="all 0.3s ease-in-out"
                _groupHover={{
                  textDecoration: 'underline'
                }}
              >
                {formatNumber(getTickSizeNumber(order.price, tickSize) || 0, 6)}
              </Text>
            </>
          ) : (
            <>
              {/* Price */}
              <Text
                position="relative"
                zIndex="1"
                pl={{ base: '0px', lg: '16px' }}
                fontSize="12px"
                fontWeight="500"
                color={priceColor}
                textAlign="left"
                flex="1"
                transition="all 0.3s ease-in-out"
                _groupHover={{
                  textDecoration: 'underline'
                }}
              >
                {formatNumber(getTickSizeNumber(order.price, tickSize) || 0, 6)}
              </Text>

              {/* Size */}
              <Text
                position="relative"
                pr={{ base: '16px', lg: '0px' }}
                zIndex="1"
                fontSize="12px"
                color="text_caption"
                textAlign="right"
                flex="1"
                transition="all 0.3s ease-in-out"
              >
                {formatNumber(order.quantity || 0, 2)}
              </Text>
            </>
          )}

          {/* Total */}
          {!isApp && (
            <Text
              position="relative"
              zIndex="1"
              pr="16px"
              fontSize="12px"
              color="text_caption"
              textAlign="right"
              flex="1"
              transition="all 0.3s ease-in-out"
            >
              {formatNumberWithKMB(order.total || 0, 4)}
            </Text>
          )}
        </HStack>
      )
    },
    [calculateDepthPercentage, maxTotal, changedOrders, orderType, setPlaceOrderPrice, tickSize]
  )

  //  Spread 
  const renderSpread = useCallback(
    () => (
      <HStack
        w="100%"
        h="36px"
        justify="flex-start"
        p="0px 16px"
        bg="card_bg"
        m={orderBookTab == 'all' ? '4px 0' : orderBookTab == 'bid' ? '4px 0 0 0' : '0'}
        gap="4px"
      >
        {!currentDeepBookPool?.price ? (
          <Skeleton isLoaded={!currentDeepBookPool} w="100%" h="33px" />
        ) : (
          <>
            <Icon
              xlinkHref="#icon-icon_arrow"
              boxW="12px"
              boxH="12px"
              svgFill={currentDeepBookPool?.priceChange?.includes('-') ? 'primary_red' : 'primary_green'}
              transform={currentDeepBookPool?.priceChange?.includes('-') ? 'rotate(0deg)' : 'rotate(180deg)'}
            />
            <Text fontSize="16px" color={currentDeepBookPool?.priceChange?.includes('-') ? 'primary_red' : 'primary_green'}>
              {formatNumber(spread, 6)}
            </Text>
          </>
        )}
      </HStack>
    ),
    [spread, currentDeepBookPool]
  )
  // console.log(' ~ OrderBookTable.tsx:336 ~ asksToShow:', asksToShow)

  const renderTableContent = useMemo(() => {
    if (orderBookTab === 'all') {
      // H5
      if (isApp) {
        return (
          <HStack w="100%" spacing="0" h="calc(100% - 34px)" justify="space-between" gap="8px">
            {/*  */}
            <VStack w="50%" spacing="4px" h="100%" justify={asksToShow?.length == visibleRows ? 'flex-end' : 'flex-start'}>
              {deepBookOrderBookLoading
                ? Array.from({ length: visibleRows }, (_, index) => <Skeleton key={`empty-bid-${index}`} w="calc(100% - 16px)" h="20px" />)
                : bidsToShow.map((bid, index) => renderOrderRow(bid, false, index, isApp))}
            </VStack>

            {/*  */}

            {/*  */}
            <VStack w="50%" spacing="4px" h="100%" justify={asksToShow?.length == visibleRows ? 'flex-end' : 'flex-start'}>
              {deepBookOrderBookLoading
                ? Array.from({ length: visibleRows }, (_, index) => <Skeleton key={`empty-ask-${index}`} w="calc(100% - 16px)" h="20px" />)
                : asksToShow.reverse().map((ask, index) => renderOrderRow(ask, true, index, isApp))}
            </VStack>
          </HStack>
        )
      }

      // PC
      return (
        <VStack w="100%" spacing="4px" h="calc(100% - 22px)" justify="center">
          {/* Asks () */}
          {deepBookOrderBookLoading
            ? Array.from({ length: visibleRows }, (_, index) => (
                <Skeleton key={`empty-ask-${index}`} w={{ base: '100%', lg: 'calc(100% - 32px)' }} h="20px" />
              ))
            : asksToShow.map((ask, index) => renderOrderRow(ask, true, index))}

          {/* Spread */}
          {renderSpread()}

          {/* Bids () */}
          {deepBookOrderBookLoading
            ? Array.from({ length: visibleRows }, (_, index) => <Skeleton key={`empty-bid-${index}`} w="calc(100% - 32px)" h="20px" />)
            : bidsToShow.map((bid, index) => renderOrderRow(bid, false, index))}
        </VStack>
      )
    } else if (orderBookTab === 'bid') {
      return (
        <VStack w="100%" spacing="4px" h="calc(100% - 22px)" justify="flex-start">
          {/* Spread */}
          {renderSpread()}

          {/*  */}
          {deepBookOrderBookLoading
            ? Array.from({ length: visibleRows }, (_, index) => <Skeleton key={`empty-bid-${index}`} w="calc(100% - 32px)" h="20px" />)
            : bidsToShow.map((bid, index) => renderOrderRow(bid, false, index))}
        </VStack>
      )
    } else if (orderBookTab === 'ask') {
      return (
        <VStack w="100%" spacing="4px" h="calc(100% - 22px)" justify="flex-end">
          {/*  */}
          {deepBookOrderBookLoading
            ? Array.from({ length: visibleRows }, (_, index) => <Skeleton key={`empty-ask-${index}`} w="calc(100% - 32px)" h="20px" />)
            : asksToShow.map((ask, index) => renderOrderRow(ask, true, index))}

          {/* Spread */}
          {renderSpread()}
        </VStack>
      )
    }
  }, [orderBookTab, asksToShow, bidsToShow, visibleRows, renderOrderRow, renderSpread, deepBookOrderBookLoading, isApp])

  return (
    <Box w="100%" h="100%" overflow="hidden">
      {/* CSS Animations -  */}
      <style>
        {`
          @keyframes slideIn {
            from {
              transform: translateX(-10px);
              opacity: 0;
            }
            to {
              transform: translateX(0);
              opacity: 1;
            }
          }
        `}
      </style>

      {/* Header */}
      {!isApp && (
        <HStack w="100%" spacing={0} justify="space-between" p="10px 16px 0px" bg="bg_secondary" position="relative" zIndex="99">
          <Text fontSize="12px" w="100%" color="primary_gray" fontWeight="500">
            Price ({currentDeepBookPool?.quoteAssets?.symbol || '--'})
          </Text>
          <Text fontSize="12px" w="100%" color="primary_gray" fontWeight="500" textAlign="right">
            Amt. ({currentDeepBookPool?.baseAssets?.symbol || '--'})
          </Text>
          <Text fontSize="12px" textAlign="right" w="100%" color="primary_gray" fontWeight="500">
            Total ({currentDeepBookPool?.quoteAssets?.symbol || '--'})
          </Text>
        </HStack>
      )}

      {isApp && (
        <HStack w="100%" spacing={0} gap="8px" justify="space-between" p="10px 16px" bg="bg_secondary" position="relative" zIndex="99">
          <HStack w="50%" justifyContent="space-between">
            <Text fontSize="12px" w="100%" color="primary_gray" fontWeight="500">
              Total ({currentDeepBookPool?.quoteAssets?.symbol || '--'})
            </Text>
            <Text fontSize="12px" w="100%" color="primary_gray" fontWeight="500" textAlign="right">
              Price
            </Text>
          </HStack>
          <HStack w="50%" justifyContent="space-between">
            <Text fontSize="12px" w="100%" color="primary_gray" fontWeight="500">
              Price
            </Text>
            <Text fontSize="12px" textAlign="right" w="100%" color="primary_gray" fontWeight="500">
              Total ({currentDeepBookPool?.quoteAssets?.symbol || '--'})
            </Text>
          </HStack>
        </HStack>
      )}

      {/* Content */}
      {renderTableContent}
    </Box>
  )
}

export default OrderBookTable
