import useDeepBookStore from '@/store/deepbook'
import { useAccountStore } from '@cetus/stores'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import useGetDeepBookOrderBook from './useGetDeepBookOrderBook'

export default function useGetCancelOrderPayload() {
  const { deepBookSDK } = usePeripherySDKStore()
  const { getRequestPool } = useGetDeepBookOrderBook()
  const { getCurrentBalanceManagerInfo } = useDeepBookStore()
  const { currentAccount } = useAccountStore()

  const getCancelOrderPayload = async (poolInfo: any, orderId: string) => {
    console.log(' ~ useGetCancelOrderPayload.ts:13 ~ getCancelOrderPayload ~ poolInfo:', poolInfo)
    console.log(' ~ useGetCancelOrderPayload.ts:13 ~ getCancelOrderPayload ~ orderId:', orderId)
    const pool = getRequestPool(poolInfo)
    console.log(' ~ useGetCancelOrderPayload.ts:15 ~ getCancelOrderPayload ~ pool:', pool)
    const deepBookAccount = getCurrentBalanceManagerInfo(currentAccount?.address as string)?.balanceManager
    const payload = await deepBookSDK.DeepbookUtils.cancelOrders(
      [
        {
          orderId,
          poolInfo: pool
        }
      ],
      deepBookAccount
    )
    return payload
  }

  const getcancellAllOrderPayload = async (orderList: any[]) => {
    const list = orderList.map((item: any) => {
      return {
        orderId: item.orderId,
        poolInfo: getRequestPool(item)
      }
    })
    const deepBookAccount = getCurrentBalanceManagerInfo(currentAccount?.address as string)?.balanceManager
    const payload = await deepBookSDK.DeepbookUtils.cancelOrders(list, deepBookAccount)
    return payload
  }

  return { getCancelOrderPayload, getcancellAllOrderPayload }
}
