import useGetDeepBookPools from '@/hooks/deepbook/useGetDeepBookPools'
import useDeepBookStore from '@/store/deepbook'
import { CetusTooltip, Loading, useGlobalToast } from '@cetus/design'
import { useDebounceFunction } from '@cetus/hooks'
import { Icon, SearchInput } from '@cetus/ui-kit'
import { Box, Button, HStack, Modal, ModalBody, ModalCloseButton, ModalContent, ModalHeader, ModalOverlay, Text, VStack } from '@chakra-ui/react'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import CoinPairInfo from '../common/CoinPairInfo'

type ManageModalProps = {
  isOpen: boolean
  onClose: (isManualClose?: boolean) => void
}
const poolList = []
export default function ManageModal({ isOpen, onClose }: ManageModalProps) {
  const [inputValue, setInputValue] = useState('')
  const { queryDeepBookPoolByValue } = useGetDeepBookPools()
  const { queryDeepBookPoolLoading, currentDeepBookPool, setDeepbookPrice } = useDeepBookStore()

  // 
  const debouncedQueryDeepBookPool = useDebounceFunction((value: string) => {
    queryDeepBookPoolByValue(value)
  }, 300)

  const handleInputChange = (value: string) => {
    const regValue = value.replace(/[^[a-zA-Z0-9\s]+$/g, '')
    setInputValue(regValue)
    if (regValue) {
      debouncedQueryDeepBookPool(regValue)
    }
  }

  const navigate = useNavigate()

  const {
    deepBookPools,
    queryDeepBookPools,
    setDeepBookPools,
    localDeepBookPools,
    setLocalDeepBookPools,
    setCurrentDeepBookPool,
    setQueryDeepBookPools
  } = useDeepBookStore()
  const { showCommonToast } = useGlobalToast()

  const importDeepBookPool = (pool: any) => {
    const newPools = [{ ...pool, isLocal: true }, ...deepBookPools] as any
    console.log(' ~ ManageModal.tsx:43 ~ importDeepBookPool ~ newPools:', newPools)
    setDeepBookPools(newPools)
    const newLocalPools = [{ ...pool, isLocal: true }, ...localDeepBookPools] as any
    console.log(' ~ ManageModal.tsx:45 ~ importDeepBookPool ~ newLocalPools:', newLocalPools)
    setLocalDeepBookPools(newLocalPools)
    setCurrentDeepBookPool({ ...pool, isLocal: true })
    setDeepbookPrice({ poolId: pool.address, price: pool.price })
    navigate(`/deepbook/${pool.address}`)
    showCommonToast('Added', 'success')

    if (inputValue) {
      const newQueryDeepBookPools = queryDeepBookPools.map((item: any) => {
        if (item.address == pool.address) {
          return { ...item, isLocal: true }
        }
        return item
      })
      setQueryDeepBookPools(newQueryDeepBookPools)
    }

    // onClose(false)
  }

  const removeDeepBookPool = (pool: any) => {
    const newPools = deepBookPools.filter((item: any) => item.address !== pool.address)
    console.log(' ~ ManageModal.tsx:54 ~ removeDeepBookPool ~ newPools:', newPools)
    setDeepBookPools(newPools)
    console.log(' ~ ManageModal.tsx:57 ~ removeDeepBookPool ~ localDeepBookPools:', localDeepBookPools)
    const newLocalPools = localDeepBookPools.filter((item: any) => item.address !== pool.address)
    setLocalDeepBookPools(newLocalPools)
    console.log(' ~ ManageModal.tsx:58 ~ removeDeepBookPool ~ currentDeepBookPool?.address:', currentDeepBookPool?.address)
    console.log(' ~ ManageModal.tsx:59 ~ removeDeepBookPool ~ pool.address:', pool.address)
    if (currentDeepBookPool?.address === pool.address) {
      console.log(' ~ ManageModal.tsx:62 ~ removeDeepBookPool ~ newPools[0]:', newPools[0])
      setCurrentDeepBookPool(newPools[0])
      setDeepbookPrice({ poolId: newPools[0]?.address, price: newPools[0]?.price })
      navigate(`/deepbook/${newPools[0]?.address}`)
    }
    console.log(' ~ ManageModal.tsx:86 ~ removeDeepBookPool ~ inputValue:', inputValue)
    if (inputValue) {
      const newQueryDeepBookPools = queryDeepBookPools
        .filter((item: any) => item.address !== inputValue)
        .map((item: any) => {
          if (item.address == pool.address) {
            return { ...item, isLocal: false }
          }
          return item
        })

      console.log(' ~ ManageModal.tsx:88 ~ removeDeepBookPool ~ newQueryDeepBookPools:', newQueryDeepBookPools)
      if (newQueryDeepBookPools.length == 0) {
        onClose(false)
        setInputValue('')
      }
      setQueryDeepBookPools(newQueryDeepBookPools)
    }
    showCommonToast('Removed', 'success')
  }

  return (
    <Modal autoFocus={false} returnFocusOnClose={false} trapFocus={false} isOpen={isOpen} onClose={() => onClose(false)} isCentered>
      <ModalOverlay />
      <ModalContent w={{ base: '100%', lg: '356px' }} overflow="hidden">
        <ModalHeader>Manage</ModalHeader>
        <ModalCloseButton />
        <ModalBody textAlign="center" p="0">
          <Box w="100%" p="0 16px 12px">
            <SearchInput
              borderRadius="8px"
              placeholder="Search by token or pool address"
              searchText={inputValue}
              onChange={(value: string) => handleInputChange(value)}
            />
          </Box>
          <VStack
            w="100%"
            gap="0px"
            h={poolList?.length > 4 ? '235px' : queryDeepBookPoolLoading ? '337px' : 'auto'}
            overflowY="auto"
            maxH="337px"
            position="relative"
          >
            {queryDeepBookPoolLoading ? (
              <Loading positionStyle="absolute" />
            ) : (
              (inputValue ? queryDeepBookPools : deepBookPools)?.map((item: any) => {
                return (
                  <HStack
                    key={item?.poolAddress}
                    w="100%"
                    borderRadius="12px"
                    p="0px 16px"
                    justify="space-between"
                    _hover={{ bg: 'primary_opacity.10' }}
                    onClick={() => {
                      if (!item.isAbandoned && !item.isLocal && !item.inWhiteList) {
                        importDeepBookPool(item)
                        onClose(false)
                      } else {
                        setCurrentDeepBookPool(item)
                        setDeepbookPrice({ poolId: item.address, price: item.price })
                        navigate(`/deepbook/${item.address}`)
                        onClose(false)
                      }
                    }}
                  >
                    <CoinPairInfo
                      showFee={false}
                      poolInfo={{
                        ...item,
                        poolAddress: item?.address,
                        displayTokenA: { ...item?.baseAssets, is_verified: true, logo_url: item?.baseAssets?.icon_url },
                        displayTokenB: { ...item?.quoteAssets, is_verified: true, logo_url: item?.quoteAssets?.icon_url }
                      }}
                      imgStyle={{ w: '28px', h: '28px', filter: item.isAbandoned ? 'grayscale(100%)' : 'none' }}
                    />
                    {item?.isAbandoned && (
                      <Text fontSize="12px" bg="primary_opacity.10" color="primary" p="4px" borderRadius="4px">
                        Sunset
                      </Text>
                    )}
                    {!item?.inWhiteList && !item?.isLocal && (
                      <Button
                        variant="ghost"
                        w="60px"
                        h="24px"
                        fontSize="12px"
                        onClick={e => {
                          e.stopPropagation()
                          importDeepBookPool(item)
                        }}
                        cursor="pointer"
                      >
                        +Add
                      </Button>
                    )}
                    {item?.isLocal && (
                      <CetusTooltip tooltip={<Text fontSize="12px">Remove</Text>} placement="top">
                        <Icon
                          xlinkHref="#icon-icon_del"
                          w="20px"
                          h="20px"
                          onClick={e => {
                            e.stopPropagation()
                            removeDeepBookPool(item)
                          }}
                        />
                      </CetusTooltip>
                    )}
                  </HStack>
                )
              })
            )}
          </VStack>
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}
