// 
const PriceChange = ({ priceChange, isLoading }: { priceChange?: string; isLoading: boolean }) => {
  if (isLoading) {
    return <Skeleton height="12px" width="50px" />
  }

  if (!priceChange || priceChange === '0%') {
    return <Text color="text_caption">{priceChange}</Text>
  }

  const isNegative = priceChange.includes('-')
  const displayValue = isNegative ? priceChange.split('-')[1] : priceChange

  return (
    <HStack gap="2px" justify="flex-end">
      <Icon
        fontSize="12px"
        xlinkHref="#icon-icon_arrow"
        transition="transform 0.5s"
        transform={isNegative ? 'rotate(0deg)' : 'rotate(180deg)'}
        svgFill={isNegative ? 'primary_red' : 'primary_green'}
      />
      <Text color={isNegative ? 'primary_red' : 'primary_green'} fontSize={{ base: '12px', lg: '14px' }}>
        {displayValue}
      </Text>
    </HStack>
  )
}

// 
const PriceDisplay = ({
  priceDisplay,
  priceUsd,
  symbol,
  isLoading
}: {
  priceDisplay?: string
  priceUsd?: string
  symbol?: string
  isLoading: boolean
}) => {
  return (
    <VStack align="flex-start" w={{ base: '50%', lg: 'auto' }}>
      <Text fontSize="12px" fontWeight="500">
        Price ({symbol || '--'})
      </Text>
      <HStack w={{ base: '100%', lg: 'auto' }}>
        {isLoading ? (
          <HStack flexDirection={{ base: 'column', lg: 'row' }} alignItems={{ base: 'flex-start', lg: 'center' }} gap={{ base: '4px', lg: '8px' }}>
            <Skeleton height={{ base: '12px', lg: '12px' }} width={{ base: '80px', lg: '60px' }} />
            <Skeleton height={{ base: '12px', lg: '12px' }} width={{ base: '100px', lg: '80px' }} />
          </HStack>
        ) : (
          <HStack flexDirection={{ base: 'column', lg: 'row' }} alignItems={{ base: 'flex-start', lg: 'center' }} gap={{ base: '4px', lg: '8px' }}>
            <Text fontSize={{ base: '12px', lg: '14px' }} fontWeight={{ base: '600', lg: '400' }} color="text_caption">
              {priceDisplay}
            </Text>
            <Text fontSize={{ base: '12px', lg: '14px' }}>{symbolDataDisplayProcessing(priceUsd)}</Text>
          </HStack>
        )}
      </HStack>
    </VStack>
  )
}

