// 
const DetailsButton = ({
  onClick,
  isOpenCard,
  setIsOpenCard,
  isLoading,
  isApp
}: {
  onClick: () => void
  setIsOpenCard: (isOpen: boolean) => void
  isOpenCard: boolean
  isLoading: boolean
  isApp: boolean
}) => {
  return (
    <Skeleton isLoaded={!isLoading}>
      <HStack cursor="pointer" _hover={{ p: { color: 'text_caption' } }}>
        <HStack onClick={onClick}>
          <Text fontSize="12px" color="primary_gray">
            <Box as="span" display={{ base: 'none', lg: 'none' }}>
              Details
            </Box>
            <Box as="span" display={{ base: 'none', lg: 'inline' }}>
              Market Details
            </Box>
          </Text>
          <Image w={{ base: '24px', lg: '20px' }} h={{ base: '24px', lg: '20px' }} src="/images/deepbook/icon_details.png" />
        </HStack>
        {isApp && (
          <Icon
            fontSize="12px"
            xlinkHref="#icon-icon_unfold"
            w="20px"
            h="20px"
            onClick={() => setIsOpenCard(!isOpenCard)}
            transform={!isOpenCard ? 'rotate(180deg)' : 'rotate(0deg)'}
            transition="transform 0.3s"
          />
        )}
      </HStack>
    </Skeleton>
  )
}

function TopDataBlock({ isOpenCard, setIsOpenCard }: { isOpenCard?: boolean; setIsOpenCard?: (isOpen: boolean) => void }) {
  const { getTokenAmountValue } = useTokenPrice()
  const { currentDeepBookPool, deepBookPoolLoading, deepbookPrice } = useDeepBookStore()
  const { windowWidth, isApp } = useWindowWidth()

  const vol24hUsdDisplay = getTokenAmountValue(currentDeepBookPool?.baseAssets?.coin_type, currentDeepBookPool?.vol24h)
  const shouldHideHighLow = windowWidth < 1340
  const isLoading = deepBookPoolLoading || !currentDeepBookPool?.quoteAssets?.symbol

  const commonProps = {
    symbol: currentDeepBookPool?.quoteAssets?.symbol,
    isLoading
  }
  const [isOpenDetails, setIsOpenDetails] = useState(false)

  //  Spread
  const spread = useMemo(() => {
    if (deepbookPrice && deepbookPrice?.price && deepbookPrice?.poolId == currentDeepBookPool?.address) {
      return currentDeepBookPool?.price
    }
    return ''
  }, [currentDeepBookPool?.address, deepbookPrice])

  const priceUsd = getTokenAmountValue(currentDeepBookPool?.quoteAssets?.coin_type, spread)

  return (
    <Block borderRadius="8px" border="none" maxHeight="100%" h={{ base: 'auto', lg: '64px' }} p={{ base: '20px 16px 24px', lg: '0 16px' }}>
      {/* H5  */}
      <VStack w="100%" gap="20px" display={{ base: 'flex', lg: 'none' }}>
        <HStack w="100%" justify="space-between">
          <ManagePool currentDeepBookPool={currentDeepBookPool} />
          <DetailsButton
            onClick={() => setIsOpenDetails(true)}
            isOpenCard={isOpenCard}
            setIsOpenCard={setIsOpenCard}
            isLoading={isLoading}
            isApp={isApp}
          />
        </HStack>

        {isOpenCard && (
          <HStack w="100%">
            <PriceDisplay priceDisplay={formatNumber(spread)} priceUsd={priceUsd} {...commonProps} />
            <VStack align="flex-start" flex="1">
              <Text fontSize="12px" fontWeight="500">
                24h Change
              </Text>
              <PriceChange priceChange={currentDeepBookPool?.priceChange} isLoading={isLoading} />
            </VStack>
          </HStack>
        )}

        {isOpenCard && (
          <HStack w="100%" justify="space-between" gap="16px">
            <VolumeDisplay vol24hDisplay={currentDeepBookPool?.vol24hDisplay} vol24hUsdDisplay={vol24hUsdDisplay} {...commonProps} />
            <HighLowDisplay high={currentDeepBookPool?.high} low={currentDeepBookPool?.low} {...commonProps} isApp={isApp} />
          </HStack>
        )}
      </VStack>

      {/* PC  */}
      <HStack w="100%" h="100%" justify="space-between" display={{ base: 'none', lg: 'flex' }}>
        <HStack gap="28px">
          <ManagePool currentDeepBookPool={currentDeepBookPool} />
          <Box h="32px" w="1px" bg="border" />

          <PriceDisplay priceDisplay={formatNumber(spread)} priceUsd={priceUsd} {...commonProps} />

          <VStack align="flex-start">
            <Text fontSize="12px" whiteSpace="nowrap" fontWeight="500">
              24h Change
            </Text>
            <PriceChange priceChange={currentDeepBookPool?.priceChange} isLoading={isLoading} />
          </VStack>

          <VolumeDisplay vol24hDisplay={currentDeepBookPool?.vol24hDisplay} vol24hUsdDisplay={vol24hUsdDisplay} {...commonProps} />

          {!shouldHideHighLow && <HighLowDisplay high={currentDeepBookPool?.high} low={currentDeepBookPool?.low} {...commonProps} />}
        </HStack>

        <DetailsButton
          onClick={() => setIsOpenDetails(true)}
          isOpenCard={isOpenCard}
          setIsOpenCard={setIsOpenCard}
          isLoading={isLoading}
          isApp={isApp}
        />
      </HStack>

      {isOpenDetails && (
        <MarketDetailsModal
          currentDeepBookPool={currentDeepBookPool}
          isOpen={isOpenDetails}
          onClose={() => setIsOpenDetails(false)}
          onCloseDetails={() => setIsOpenDetails(false)}
        />
      )}
    </Block>
  )
}

export default TopDataBlock
