import useDeepBookOrderActions from '@/hooks/deepbook/useDeepBookOrderActions'
import useDeepBookStore from '@/store/deepbook'
import { useAccountStore } from '@cetus/stores'
import { NoData, Table } from '@cetus/ui-kit'
import { formatNumber } from '@cetus/utils'
import { Button, HStack, Text } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'
import CoinPairInfo from '../common/CoinPairInfo'
import TypeCon from '../common/proModeAndChart/ProModeTradeTab/TypeCon'

export default function OpenOrdersTableBlock() {
  const { deepBookOpenOrders, cancelOrderLoading, orderListLoading, setShowOpenOrdersNum } = useDeepBookStore()
  const { cancelOrder } = useDeepBookOrderActions()
  const [type, setType] = useState('')
  const { currentAccount, onWalletModal } = useAccountStore()

  const dataSource = useMemo(() => {
    return deepBookOpenOrders.filter((item: any) => (!type ? true : type.split(',').includes(item.side)))
  }, [deepBookOpenOrders, type])

  useEffect(() => {
    setShowOpenOrdersNum(dataSource?.length)
  }, [dataSource?.length])

  return (
    <Table
      dataSource={dataSource}
      columns={getColumns(type, setType, cancelOrder, cancelOrderLoading)}
      loading={orderListLoading}
      // isFlexStart
      fixedHeader
      // trPadding="16px"
      headBg="bg_secondary"
      noData={
        !currentAccount?.address ? (
          <NoData type="nowallet" noBorder bg="none" onboard={() => onWalletModal(true)} />
        ) : dataSource?.length == 0 ? (
          <NoData type="nodata" text="No orders" noBorder bg="none" />
        ) : undefined
      }
      tableContainerWrapStyle={{
        h: '100%'
      }}
    />
  )
}

const getColumns = (
  type: string,
  setType: (val: string) => void,
  cancelOrder: (poolInfo: any, orderId: string) => void,
  cancelOrderLoading: boolean
) => {
  return [
    {
      title: (
        <Text color="primary_gray" fontSize="12px" pt="8px" fontWeight="500">
          Market
        </Text>
      ),
      key: '#',
      thConfig: {
        w: '30%'
      },
      render: (item: any, index: number) => (
        <HStack h="32px">
          <CoinPairInfo
            poolInfo={{
              displayTokenA: item?.baseAssets,
              displayTokenB: item?.quoteAssets,
              poolAddress: item?.poolId
            }}
            imgStyle={{
              w: '24px',
              h: '24px'
            }}
            showFee={false}
            coinPairInfoWrapStyle={{
              p: '0px'
            }}
          />
        </HStack>
      )
    },
    {
      title: (
        <HStack justifyContent="flex-end" pt="8px" height="24px" fontWeight="500">
          <TypeCon type={type} setType={setType} filterList={['Buy', 'Sell']} labelText="Side" />
        </HStack>
      ),
      key: 'side',
      thConfig: {
        w: '18%'
      },
      render: (item: any) => {
        const colorMap: any = {
          Buy: { color: 'primary_green', bg: 'primary_green_opacity.10' },
          Sell: { color: 'primary_red', bg: 'primary_red_opacity.10' }
        }
        const { color, bg } = colorMap[item?.side]
        return (
          <HStack w="100%" justifyContent="flex-end">
            <Text fontSize="12px" color={color} bg={bg} h="20px" p="0 8px" borderRadius="4px" lineHeight="20px">
              {item?.side?.[0]?.toUpperCase() + item?.side?.slice(1)}
            </Text>
          </HStack>
        )
      }
    },
    {
      title: (
        <Text color="primary_gray" fontSize="12px" textAlign="right" pt="8px" fontWeight="500">
          Price
        </Text>
      ),
      key: 'price',
      thConfig: {
        w: '18%'
      },
      render: (item: any) => (
        <Text w="100%" textAlign="right" color="text_caption">
          {formatNumber(item?.price)}
        </Text>
      )
    },
    {
      title: (
        <Text color="primary_gray" fontSize="12px" textAlign="right" pt="8px" fontWeight="500">
          Filled/Qty
        </Text>
      ),
      key: 'filled',
      thConfig: {
        w: '18%'
      },
      render: (item: any) => (
        <HStack w="100%" justify="flex-end" gap="0px">
          <Text color="primary">{formatNumber(item?.filledQuantity)}</Text>
          <Text>/</Text>
          <Text color="text_caption">{formatNumber(item?.originalQuantity)}</Text>
        </HStack>
      )
    },
    {
      title: (
        <Text color="primary_gray" fontSize="12px" pt="8px" fontWeight="500">
          Action
        </Text>
      ),
      key: 'action',
      thConfig: {
        w: '18%'
      },
      render: (item: any) => (
        <HStack w="100%" justify="flex-end">
          <Button
            w="80px"
            h="24px"
            borderRadius="8px"
            fontSize="12px"
            variant="ghost"
            borderColor="transparent !important"
            onClick={() => cancelOrder(item, item.orderId)}
            isLoading={cancelOrderLoading}
          >
            Cancel
          </Button>
        </HStack>
      )
    }
  ]
}
