import useDeepBookOrderActions from '@/hooks/deepbook/useDeepBookOrderActions'
import useDeepBookStore from '@/store/deepbook'
import { Block, SelectTab } from '@cetus/design'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { HStack, StackProps, Switch, Text, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import OpenOrdersTableBlock from './OpenOrdersTableBlock'
import SettledBalanceTableBlock from './SettledBalanceTableBlock'
import TradeHistoryTableBlock from './TradeHistoryTableBlock'

const ActionContent = ({
  isShowAllMarkets,
  isCheckedAllMarkets,
  setIsCheckedAllMarkets,
  isShowCancelAll,
  cancelAllOrder,
  wrapStyle
}: {
  isShowAllMarkets: boolean
  isCheckedAllMarkets: boolean
  setIsCheckedAllMarkets: (isChecked: boolean) => void
  isShowCancelAll: boolean
  cancelAllOrder: () => void
  wrapStyle: StackProps
}) => {
  const { isApp } = useWindowWidth()
  return (
    <HStack gap="12px" w={{ base: '100%', lg: 'auto' }} {...wrapStyle}>
      {isShowAllMarkets && (
        <HStack _hover={{ cursor: 'pointer', _sx: { '& > p': { color: 'text_caption' } } }}>
          <Text whiteSpace="nowrap" fontSize="12px">
            All Markets
          </Text>
          <Switch isChecked={isCheckedAllMarkets} onChange={() => setIsCheckedAllMarkets(!isCheckedAllMarkets)} />
        </HStack>
      )}
      {isShowCancelAll && (
        <Text whiteSpace="nowrap" fontSize="12px" _hover={{ cursor: 'pointer', color: 'text_caption' }} onClick={() => cancelAllOrder()}>
          Cancel All
        </Text>
      )}
    </HStack>
  )
}

const TradeTableTab = ({
  tabList,
  currentTab,
  setCurrentTab,
  isCheckedAllMarkets,
  setIsCheckedAllMarkets,
  isShowCancelAll,
  cancelAllOrder,
  isShowAllMarkets
}: {
  tabList: any[]
  currentTab: any
  setCurrentTab: (tab: any) => void
  isCheckedAllMarkets: boolean
  setIsCheckedAllMarkets: (isChecked: boolean) => void
  isShowCancelAll: boolean
  cancelAllOrder: () => void
  isShowAllMarkets: boolean
}) => {
  const { isApp } = useWindowWidth()

  return (
    <HStack p="0 16px" overflow="auto">
      <HStack borderBottom="1px solid" borderColor="border" minW={{ base: '440px', lg: 'auto' }} w="100%">
        <SelectTab<any, any>
          type="borderTab"
          tabList={tabList}
          currentTab={currentTab}
          handleChangeTab={tab => setCurrentTab(tab?.label)}
          wrapStyle={{
            w: '100%',
            h: '44px',
            border: 'none',
            gap: '40px'
          }}
          itemStyle={{
            h: '100%',
            borderRadius: '4px',
            gap: '4px',
            justifyContent: 'center',
            alignItems: 'center',
            fontSize: '14px',
            fontWeight: '500',
            whiteSpace: 'nowrap'
          }}
          selectTabItemTextStyle={{
            color: 'primary_gray'
          }}
        />
        {!isApp && (
          <ActionContent
            isShowAllMarkets={isShowAllMarkets}
            isCheckedAllMarkets={isCheckedAllMarkets}
            setIsCheckedAllMarkets={setIsCheckedAllMarkets}
            isShowCancelAll={isShowCancelAll}
            cancelAllOrder={cancelAllOrder}
          />
        )}
      </HStack>
    </HStack>
  )
}

function TradeTableBlock() {
  const {
    isCheckedAllMarkets,
    setIsCheckedAllMarkets,
    deepBookOpenOrders,
    deepBookPools,
    deepBookOrderHistory,
    currentDeepBookPool,
    orderListLoading,
    deepBookOrderHistoryLoading,
    showOpenOrdersNum,
    showDeepBookOrderHistoryNum
  } = useDeepBookStore()

  const { cancelAllOrder } = useDeepBookOrderActions()

  const { currentAccount } = useAccountStore()
  const [currentTab, setCurrentTab] = useState<any>('Open Orders')
  const { isApp } = useWindowWidth()

  const tradeTableTabList = useMemo(() => {
    return [
      {
        label: 'Open Orders',
        num: showOpenOrdersNum > 0 && currentAccount?.address && !orderListLoading ? showOpenOrdersNum : undefined
      },
      {
        label: 'Trade History',
        num: showDeepBookOrderHistoryNum > 0 && currentAccount?.address && !deepBookOrderHistoryLoading ? showDeepBookOrderHistoryNum : undefined
      },
      {
        label: 'Settled Balance',
        tooltip:
          'Settled balance will be claimed automatically to your free balance if you conduct new actions in the same pool. Or you could manually claim it from here.',
        afterIcon: { xlinkHref: '#icon-icon_tips', fontSize: '20px' },
        tooltipPlacement: 'top'
      }
    ]
  }, [currentAccount?.address, currentTab, orderListLoading, deepBookOrderHistoryLoading, showOpenOrdersNum, showDeepBookOrderHistoryNum])

  return (
    <Block p="0px" h="100%" borderRadius="8px" border="none">
      <TradeTableTab
        tabList={tradeTableTabList}
        currentTab={currentTab}
        setCurrentTab={setCurrentTab}
        isCheckedAllMarkets={isCheckedAllMarkets}
        setIsCheckedAllMarkets={setIsCheckedAllMarkets}
        isShowCancelAll={currentTab == 'Open Orders' && deepBookOpenOrders?.length > 0}
        isShowAllMarkets={currentTab !== 'Settled Balance'}
        cancelAllOrder={cancelAllOrder}
      />
      {isApp && (
        <ActionContent
          isShowAllMarkets={currentTab !== 'Settled Balance'}
          isCheckedAllMarkets={isCheckedAllMarkets}
          setIsCheckedAllMarkets={setIsCheckedAllMarkets}
          isShowCancelAll={currentTab == 'Open Orders' && deepBookOpenOrders?.length > 0}
          cancelAllOrder={cancelAllOrder}
          wrapStyle={{
            p: '8px 16px 0px',
            justifyContent: 'space-between'
          }}
        />
      )}
      <VStack w="100%" h={{ base: '180px', lg: 'calc(100% - 44px)' }}>
        {currentTab == 'Open Orders' && <OpenOrdersTableBlock />}
        {currentTab == 'Trade History' && <TradeHistoryTableBlock />}
        {currentTab == 'Settled Balance' && <SettledBalanceTableBlock />}
      </VStack>
    </Block>
  )
}

export default TradeTableBlock
