import useGetDeepBookBalance from '@/hooks/deepbook/useGetDeepBookBalance'
import { SelectTab, TradeInput } from '@cetus/design'
import { Tab } from '@cetus/design/src/components/common/SelectTab'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import { Token } from '@cetus/types'
import { formatNumberWithDown } from '@cetus/utils'
import { d } from '@cetusprotocol/common-sdk'
import { CoinAssist } from '@cetusprotocol/deepbook-utils'
import {
  Box,
  Button,
  HStack,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  Slider,
  SliderFilledTrack,
  SliderThumb,
  SliderTrack,
  Text,
  VStack
} from '@chakra-ui/react'
import { useCallback, useEffect, useMemo, useState } from 'react'

type AssetsInfoModalProps = {
  token: Token
  title: 'Deposit' | 'Withdraw'
  isOpen: boolean
  onClose: (isManualClose?: boolean) => void
  onConfirm: (amount: string) => void
  isBaseAsset: boolean
}

// 
type InputSource = 'slider' | 'input' | 'tab'

export default function AssetsInfoModal({ token, title, isOpen, onClose, onConfirm, isBaseAsset }: AssetsInfoModalProps) {
  const [inputValue, setInputValue] = useState<string>('')
  const [slideValue, setSlideValue] = useState<number>(25)
  const [inputSource, setInputSource] = useState<InputSource>('slider')

  const tabList: Tab[] = [{ label: '25%' }, { label: '50%' }, { label: '75%' }, { label: 'MAX' }]
  const { getTokenAmountValue } = useTokenPrice()
  const amountValue = getTokenAmountValue(token?.coin_type, inputValue)

  const { baseFreeBalance, quoteFreeBalance, baseBalance, quoteBalance } = useGetDeepBookBalance()

  // 
  const currentBalance = useMemo(() => {
    if (title === 'Deposit') {
      return isBaseAsset ? baseBalance?.balanceFormat : quoteBalance?.balanceFormat
    }
    return isBaseAsset ? baseFreeBalance : quoteFreeBalance
  }, [title, isBaseAsset, baseBalance?.balanceFormat, quoteBalance?.balanceFormat, baseFreeBalance, quoteFreeBalance])

  //  SUI 
  const isSuiCoin = useMemo(() => {
    return CoinAssist.isSuiCoin(token?.coin_type || '')
  }, [token?.coin_type])

  const currentTab = useMemo(() => {
    return slideValue === 100 ? 'MAX' : `${slideValue}%`
  }, [slideValue])

  const changeSlideValue = useCallback((value: string | number, source: InputSource = 'slider') => {
    setInputSource(source)
    if (value === 'MAX' || value === 100) {
      setSlideValue(100)
    } else {
      const percent = Number(String(value).replace('%', ''))
      setSlideValue(percent)
    }
  }, [])

  const handleTabChange = useCallback(
    (tab: Tab) => {
      if (tab.label === 'MAX') {
        changeSlideValue(100, 'tab')
      } else {
        changeSlideValue(tab.label, 'tab')
      }
    },
    [changeSlideValue]
  )

  const handleSliderChange = useCallback(
    (value: number) => {
      changeSlideValue(`${value}%`, 'slider')
    },
    [changeSlideValue]
  )

  const handleInputChange = useCallback((value: string) => {
    setInputSource('input')
    setInputValue(value)
  }, [])

  // /
  useEffect(() => {
    console.log(' ~ AssetsInfoModal.tsx:103 ~ AssetsInfoModal ~ slideValue:', slideValue)
    if (slideValue && (inputSource === 'slider' || inputSource === 'tab') && currentBalance) {
      const remainAmount = isSuiCoin && title == 'Deposit' && slideValue == 100 ? 0.05 : 0
      const value = formatNumberWithDown(d(currentBalance).mul(slideValue).div(100).sub(remainAmount).toString(), token?.decimals, true)
      setInputValue(value)
    }
  }, [slideValue, inputSource, currentBalance, token?.decimals, isSuiCoin, title])

  // 
  useEffect(() => {
    if (inputValue && inputSource === 'input' && currentBalance) {
      console.log(' ~ AssetsInfoModal.tsx:112 ~ AssetsInfoModal ~ inputValue:', inputValue)
      const remainAmount = isSuiCoin && title == 'Deposit' ? 0.05 : 0
      const percentage = d(inputValue).add(remainAmount).div(d(currentBalance)).mul(100)
      const clampedPercentage = d(percentage).gt(100) ? 100 : Number(percentage)
      console.log(' ~ AssetsInfoModal.tsx:118 ~ AssetsInfoModal ~ clampedPercentage:', clampedPercentage)
      setSlideValue(clampedPercentage)
    }
  }, [inputValue, inputSource, currentBalance])

  // 
  // useEffect(() => {
  //   console.log(':', inputSource, ':', inputValue, ':', slideValue)
  // }, [inputSource, inputValue, slideValue])

  const btnInfo = useMemo(() => {
    if (!+inputValue) {
      return { text: '', disabled: true }
    }

    const inputAmount = d(inputValue)
    const hasInsufficientBalance = inputAmount.gt(currentBalance || 0)

    if (hasInsufficientBalance) {
      return {
        text: `Insufficient ${token?.symbol} balance`,
        disabled: true
      }
    }

    return {
      text: title === 'Deposit' ? 'Deposit' : 'Withdraw',
      disabled: false
    }
  }, [inputValue, currentBalance, token?.symbol, title])

  return (
    <Modal autoFocus={false} returnFocusOnClose={false} trapFocus={false} isOpen={isOpen} onClose={() => onClose(false)} isCentered>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>{title}</ModalHeader>
        <ModalCloseButton />
        <ModalBody textAlign="center" p="0 16px 16px">
          <VStack w="100%" gap="20px">
            <TradeInput
              wrapStyle={{ h: 'unset' }}
              placeholder="0.0"
              value={inputValue}
              needRemainBalance={title === 'Deposit' && isSuiCoin}
              amountValue={amountValue}
              onChange={handleInputChange}
              balance={currentBalance}
              token={token}
              balanceLabel={title === 'Deposit' ? '' : 'Available'}
              inputStyle={{ width: '100%' }}
              half={false}
              max={false}
            />

            <HStack w="100%" justify="space-between">
              <Text fontSize="20px" color="text_caption" whiteSpace="nowrap">
                {d(slideValue).gt(0) && slideValue < 0.01 ? '<0.01' : `${formatNumberWithDown(slideValue, 2)}%`}
              </Text>

              <SelectTab
                type="outlineTab"
                tabList={tabList}
                currentTab={currentTab}
                handleChangeTab={handleTabChange}
                wrapStyle={{
                  w: { base: '220px', lg: '350px' },
                  h: '32px',
                  p: '3px',
                  borderRadius: '8px'
                }}
                itemStyle={{
                  flex: 1,
                  fontSize: '14px',
                  margin: 0
                }}
              />
            </HStack>

            <Box w="100%" mt="-10px">
              <Slider aria-label="assets-info-slider" min={0} max={100} focusThumbOnChange={false} value={slideValue} onChange={handleSliderChange}>
                <SliderTrack bg="bg_secondary" h="8px">
                  <SliderFilledTrack h="4px" />
                </SliderTrack>
                <SliderThumb />
              </Slider>
            </Box>

            <Button w="100%" fontWeight="500" onClick={() => onConfirm(inputValue)} isDisabled={btnInfo.disabled}>
              {btnInfo.text || title}
            </Button>
          </VStack>
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}
