import { DeepBookTradeHistoryPath } from '@/apis/path'
import useDeepBookStore from '@/store/deepbook'
import { useFetch } from '@cetus/hooks'
import { useAccountStore } from '@cetus/stores'
import { d, timeFormatUTC } from '@cetus/utils'
import { useMemo } from 'react'
import useGetDeepBookPools from './useGetDeepBookPools'

export default function useGetDeepBookOrderHistory() {
  const { fetchByApi } = useFetch()
  const { currentAccount } = useAccountStore()
  const { deepBookPoolsObj, setDeepBookOrderHistoryLoading, setDeepBookOrderHistory } = useDeepBookStore()
  const { queryDeepBookPoolByValue } = useGetDeepBookPools()

  // 
  const poolInfoCache = useMemo(() => new Map(), [])

  const getDeepBookOrderHistory = async () => {
    try {
      setDeepBookOrderHistoryLoading(true)
      const res = await fetchByApi(DeepBookTradeHistoryPath, 'GET', { address: currentAccount?.address, limit: 9999 })
      console.log(' ~ useGetDeepBookOrderHistort.ts:11 ~ getDeepBookOrderHistory ~ res:', res)

      //  pool_id
      const poolIdsToQuery = new Set<string>()
      res?.list?.forEach((item: any) => {
        if (!deepBookPoolsObj[item.pool_id] && !poolInfoCache.has(item.pool_id)) {
          poolIdsToQuery.add(item.pool_id)
        }
      })

      // 
      if (poolIdsToQuery.size > 0) {
        console.log(' ~ :', Array.from(poolIdsToQuery))
        const poolIdsArray = Array.from(poolIdsToQuery)

        // 
        for (const poolId of poolIdsArray) {
          try {
            const poolInfo = await queryDeepBookPoolByValue(poolId)
            if (poolInfo && poolInfo.length > 0) {
              poolInfoCache.set(poolId, poolInfo[0])
            }
          } catch (error) {
            console.error(` poolId: ${poolId}`, error)
          }
        }
      }

      const list = []
      for (let i = 0; i < res?.list?.length; i++) {
        const item = res?.list[i]
        const history = wrapDeepBookOrderHistory(item)
        list.push(history)
      }
      setDeepBookOrderHistory(list)
      setDeepBookOrderHistoryLoading(false)
      return list
    } catch (error) {
      console.log(' ~ useGetDeepBookOrderHistory.ts:15 ~ getDeepBookOrderHistory ~ error:', error)
      setDeepBookOrderHistoryLoading(false)
      return []
    }
  }

  const wrapDeepBookOrderHistory = (history: any) => {
    //  store 
    let poolInfo = deepBookPoolsObj[history.pool_id]

    //  store 
    if (!poolInfo) {
      poolInfo = poolInfoCache.get(history.pool_id)
      console.log(' ~ :', poolInfo)
    }

    // 
    if (!poolInfo) {
      console.warn(` poolId: ${history.pool_id}`)
      return null
    }

    const { baseAssets, quoteAssets } = poolInfo
    const quoteDecimals = quoteAssets.decimals
    const baseDecimals = baseAssets.decimals
    const { block_time, is_bid, pool_id, tx } = history
    const price = d(history.price)
      .div(Math.pow(10, quoteDecimals - baseDecimals + 9))
      .toString()
    // 
    const quantityFilled = d(history.base_asset_quantity_filled).div(Math.pow(10, baseDecimals)).toString()
    // 
    const originalQuantity = d(history.original_quantity).div(Math.pow(10, baseDecimals)).toString()
    //coin
    const filled = d(quantityFilled).mul(d(price)).toString()
    // 
    const orderStatus = 'Success'

    return {
      baseAssets,
      quoteAssets,
      blockTime: timeFormatUTC(block_time),
      side: is_bid ? 'Buy' : 'Sell',
      price,
      filled,
      fee: 0,
      orderStatus,
      total: quantityFilled,
      poolId: pool_id,
      tx
    }
  }

  return { getDeepBookOrderHistory }
}
