import useDeepBookStore from '@/store/deepbook'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { CoinPairImage, Icon, Table } from '@cetus/ui-kit'
import { Box, Button, HStack, Menu, MenuButton, MenuItem, MenuList, Skeleton, Text, useDisclosure } from '@chakra-ui/react'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import ManageModalModal from './ManageModal'

function ManagePool({ currentDeepBookPool }: { currentDeepBookPool: any }) {
  const menuDisclosure = useDisclosure()
  const [isOpenManageModal, setIsOpenManageModal] = useState(false)
  const [isHover, setIsHover] = useState(false)
  const navigate = useNavigate()
  const { deepBookPools, setCurrentDeepBookPool, setDeepbookPrice } = useDeepBookStore()
  const { isApp } = useWindowWidth()

  return (
    <HStack
      w={{ base: 'auto', lg: 'auto' }}
      position="relative"
      zIndex="100"
      cursor="pointer"
      onMouseEnter={() => setIsHover(true)}
      onMouseLeave={() => setIsHover(false)}
    >
      <Menu isOpen={menuDisclosure.isOpen} onClose={menuDisclosure.onClose} placement="bottom-start" gutter={8} strategy="fixed">
        <MenuButton
          as={Button}
          bg="none"
          _hover={{ bg: 'none' }}
          _active={{ bg: 'none' }}
          p="0"
          onClick={menuDisclosure.onOpen}
          w={{ base: 'auto', lg: 'auto' }}
          h="40px"
        >
          <Skeleton isLoaded={currentDeepBookPool?.price}>
            <HStack>
              <CoinPairImage
                coinACoinType={currentDeepBookPool?.baseAssets?.coin_type}
                coinBCoinType={currentDeepBookPool?.quoteAssets?.coin_type}
                coinAIconUrl={currentDeepBookPool?.baseAssets?.icon_url}
                coinBIconUrl={currentDeepBookPool?.quoteAssets?.icon_url}
              />
              <Text fontSize="16px" color="text_caption" fontWeight="500">
                {`${currentDeepBookPool?.baseAssets?.symbol || ''} - ${currentDeepBookPool?.quoteAssets?.symbol || ''}`}
              </Text>
              <Icon
                fontSize="12px"
                xlinkHref="#icon-icon_arrow"
                transition="transform 0.5s"
                transform={menuDisclosure.isOpen ? 'rotate(180deg)' : 'rotate(0deg)'}
                svgFill={isHover ? 'text_caption' : 'text_paragraph'}
              />
            </HStack>
          </Skeleton>
        </MenuButton>
        <MenuList
          w={{ base: '370px', lg: '466px' }}
          borderRadius="12px"
          p="0px"
          mt="0"
          zIndex="9999"
          maxH="400px"
          overflow="hidden"
          boxShadow="0px 4px 20px rgba(0, 0, 0, 0.15)"
        >
          <MenuItem closeOnSelect={false} p="0" w="100%">
            <Box w="100%" maxH="240px" overflowY="auto" p="4px 0px" sx={{ scrollbarGutter: 'stable both-edges' }}>
              <Table
                rowKey="address"
                columns={getColumns(isApp)}
                dataSource={deepBookPools}
                skeletonLength={3}
                loading={false}
                trPadding="12px"
                tableContainerWrapStyle={{
                  minH: '200px'
                }}
                onRowClick={(item: any) => {
                  setCurrentDeepBookPool(item)
                  setDeepbookPrice({ poolId: item.address, price: item.price })
                  navigate(`/deepbook/${item.address}`)
                  menuDisclosure.onClose()
                }}
                rowStyle={{
                  h: '52px',
                  cursor: 'pointer',
                  _hover: { bg: 'primary_opacity.10)' }
                }}
              />
            </Box>
          </MenuItem>
          <HStack justify="center" w="100%" borderTop="1px solid" borderColor="border">
            <Button
              w="166px"
              m="16px"
              h="28px"
              borderRadius="8px"
              fontSize="14px"
              variant="outline"
              onClick={() => {
                setIsOpenManageModal(true)
                menuDisclosure.onClose()
              }}
            >
              Manage Pool
            </Button>
          </HStack>
        </MenuList>
      </Menu>
      {isOpenManageModal && <ManageModalModal isOpen={isOpenManageModal} onClose={() => setIsOpenManageModal(false)} />}
    </HStack>
  )
}

const getColumns = (isApp: boolean) => {
  return [
    {
      title: (
        <Text fontSize="12px" fontWeight="500">
          Pool
        </Text>
      ),
      thConfig: {
        w: '50%'
      },
      key: 'token',
      render: (item: any) => {
        return (
          <HStack justify="flex-start" sx={{ minW: 'unset', p: { margin: '0' } }} m="-12px 0">
            <Skeleton isLoaded={true}>
              <HStack>
                <HStack filter={item?.isAbandoned ? 'grayscale(100%)' : 'none'}>
                  <CoinPairImage
                    coinACoinType={item?.baseAssets?.coin_type}
                    coinBCoinType={item?.quoteAssets?.coin_type}
                    coinAIconUrl={item?.baseAssets?.icon_url}
                    coinBIconUrl={item?.quoteAssets?.icon_url}
                    w="28px"
                    h="28px"
                  />
                  <Text fontSize="14px" color="text_caption">
                    {`${item?.baseAssets?.symbol || ''} - ${item?.quoteAssets?.symbol || ''}`}
                  </Text>
                </HStack>
                {item?.isAbandoned && (
                  <Text fontSize="12px" bg="primary_opacity.10" color="primary" p="2px 4px" borderRadius="4px">
                    Sunset
                  </Text>
                )}
              </HStack>
            </Skeleton>
          </HStack>
        )
      }
    },
    {
      title: (
        <Text fontSize="12px" fontWeight="500" textAlign="right">
          Price
        </Text>
      ),
      thConfig: {
        w: '25%'
      },
      key: 'price24h',
      render: ({ priceDisplay }: { priceDisplay: string }) => {
        return (
          <HStack gap="0" justify="flex-end">
            <Text color="text_caption">{priceDisplay}</Text>
          </HStack>
        )
      }
    },
    !isApp && {
      title: (
        <Text fontSize="12px" fontWeight="500">
          24H Change
        </Text>
      ),
      thConfig: {
        w: '25%'
      },
      key: 'vol24h',
      render: ({ priceChange }: { priceChange: string }) => {
        return (
          <HStack gap="2px" justify="flex-end">
            {priceChange !== '0%' && (
              <Icon
                fontSize="12px"
                xlinkHref="#icon-icon_arrow"
                transition="transform 0.5s"
                transform={priceChange?.includes('-') ? 'rotate(0deg)' : 'rotate(180deg)'}
                svgFill={priceChange?.includes('-') ? 'primary_red' : 'primary_green'}
              />
            )}
            <Text color={priceChange?.includes('-') ? 'primary_red' : priceChange === '0%' ? 'text_caption' : 'primary_green'}>
              {priceChange?.includes('-') ? priceChange?.split('-')[1] : priceChange}
            </Text>
          </HStack>
        )
      }
    }
  ]
}
export default ManagePool
