import { formatNumber } from '@cetus/utils'
import { Text, VStack } from '@chakra-ui/react'
import InputBlock from './InputBlock'

export type InputBlockProps = {
  title: string
  symbol: string
  value: string
  onChange: (value: string) => void
  isMarket?: boolean
  decimals?: number
}

type InputBlockGroupProps = {
  from: InputBlockProps
  to: InputBlockProps
  isShowOrderVolumeError?: boolean
  minSize?: string
  isMarket?: boolean
}

const OrderVolumeError = ({ minSize, baseSymbol }: { minSize: string; baseSymbol: string }) => {
  return (
    <VStack w="100%" h="44px" borderRadius="12px" bg="primary_yellow_opacity.10" mt="-30px" justifyContent="flex-end" pb="4px">
      <Text fontSize="12px" color="primary_yellow">
        Order volume must be greater than {formatNumber(minSize)} {baseSymbol}
      </Text>
    </VStack>
  )
}
export default function InputBlockGroup(props: InputBlockGroupProps) {
  const { from, to, isShowOrderVolumeError, minSize, isMarket } = props
  return (
    <VStack w="100%" borderRadius="8px" bg="bg_secondary" position="relative" zIndex="5">
      <InputBlock {...from} isMarket={isMarket} />
      <InputBlock {...to} />
      {isShowOrderVolumeError && <OrderVolumeError minSize={minSize as string} baseSymbol={to.symbol} />}
    </VStack>
  )
}
