import useGetDeepBookManagerBalance from '@/hooks/deepbook/useGetDeepBookManagerBalance'
import useGetDeepBookOpenOrders from '@/hooks/deepbook/useGetDeepBookOpenOrders'
import useTradeCard from '@/hooks/deepbook/useTradeCard'
import useDeepBookStore from '@/store/deepbook'
import { SelectTab } from '@cetus/design'
import { useAccountBalance } from '@cetus/hooks'
import { useAccountStore } from '@cetus/stores'
import { HTextLabelBox, Icon, RefreshButton } from '@cetus/ui-kit'
import { Decimal, formatNumber, formatNumberWithKMB } from '@cetus/utils'
import { Button, HStack, Skeleton, Text, VStack } from '@chakra-ui/react'
import Slippage from '../common/Slippage'
import InputBlockGroup from './InputBlockGroup'

const tradeBlockTabList = [
  {
    label: 'Buy'
  },
  {
    label: 'Sell'
  }
]
const TradeBlockTab = ({ currentTab, setCurrentTab }: { currentTab: 'Buy' | 'Sell'; setCurrentTab: (tab: 'Buy' | 'Sell') => void }) => {
  return (
    <SelectTab<any, any>
      type="outlineTab"
      tabList={tradeBlockTabList}
      currentTab={currentTab}
      handleChangeTab={tab => setCurrentTab(tab?.label)}
      wrapStyle={{
        w: '100%',
        h: '40px',
        p: '4px',
        border: '1px solid',
        borderColor: 'border',
        borderRadius: '8px',
        gap: '4px',
        zIndex: '99'
      }}
      itemStyle={{
        w: '50%',
        h: '100%',
        p: '4px 8px',
        borderRadius: '4px',
        gap: '4px',
        justifyContent: 'center',
        alignItems: 'center',
        fontSize: '14px',
        fontWeight: '500',
        //  Chakra UI  sx 
        sx: {
          '&[data-active="true"]': {
            backgroundColor: currentTab == 'Buy' ? 'primary_green_opacity.10' : 'primary_red_opacity.10',
            color: currentTab == 'Buy' ? 'primary_green' : 'primary_red'
          }
        }
      }}
      selectTabItemTextStyle={{
        color: 'primary_gray',
        sx: {
          '&[data-active="true"]': {
            color: currentTab == 'Buy' ? 'primary_green' : 'primary_red'
          }
        }
      }}
    />
  )
}

const orderBlockTabList = [
  {
    label: 'Market'
  },
  {
    label: 'Limit'
  }
]
const OrderBlockTab = ({ currentTab, setCurrentTab }: { currentTab: 'Market' | 'Limit'; setCurrentTab: (tab: 'Market' | 'Limit') => void }) => {
  const { currentDeepBookPool, isCheckedAllMarkets } = useDeepBookStore()
  const { getManagerBalance } = useGetDeepBookManagerBalance()
  const { currentAccount } = useAccountStore()
  const { getCurrentBalanceManagerInfo } = useDeepBookStore()
  const currentBalanceManagerInfo = getCurrentBalanceManagerInfo(currentAccount?.address as string)
  const { fetchAccountBalance } = useAccountBalance()
  const { getDeepBookAllOpenOrders, getDeepBookOpenOrders } = useGetDeepBookOpenOrders()

  const handleRefresh = () => {
    getManagerBalance(
      [
        { coin_type: currentDeepBookPool?.baseAssets.coin_type, decimals: currentDeepBookPool?.baseAssets.decimals },
        { coin_type: currentDeepBookPool?.quoteAssets.coin_type, decimals: currentDeepBookPool?.quoteAssets.decimals }
      ],
      currentAccount?.address as string,
      currentBalanceManagerInfo?.balanceManager
    )
    fetchAccountBalance()
    if (isCheckedAllMarkets) {
      getDeepBookAllOpenOrders()
    } else {
      getDeepBookOpenOrders(currentDeepBookPool)
    }
  }
  return (
    <HStack w="100%" borderRadius="8px" bg="bg_secondary" justifyContent="space-between">
      <SelectTab<any, any>
        type="borderTab"
        tabList={orderBlockTabList}
        currentTab={currentTab}
        handleChangeTab={tab => setCurrentTab(tab?.label)}
        wrapStyle={{
          w: '132px',
          h: '40px',
          border: 'none',
          gap: '32px',
          pl: '4px'
        }}
        itemStyle={{
          w: '50%',
          h: '100%',
          borderRadius: '4px',
          gap: '4px',
          justifyContent: 'center',
          alignItems: 'center',
          fontSize: '14px',
          fontWeight: '500'
        }}
        selectTabItemTextStyle={{
          color: 'primary_gray'
        }}
      />
      <HStack>
        {currentTab == 'Market' && <Slippage slippageType="deepbook" />}
        <RefreshButton w="28px" h="28px" innerStyle={{ w: '26px', h: '26px', bg: 'none', fontSize: '12px' }} handleRefresh={handleRefresh} />
      </HStack>
    </HStack>
  )
}

const OrderInfoBlock = ({
  currentOrderType,
  isShowOrderVolumeError,
  estTotal,
  currentDeepBookPool,
  estTotalUsd,
  available,
  maxFee,
  maxFeeIsLoading,
  tradeType,
  total,
  isLoading
}: {
  currentOrderType: 'Market' | 'Limit'
  isShowOrderVolumeError: boolean
  estTotal: string
  currentDeepBookPool: any
  estTotalUsd: string
  available: string
  maxFee: string
  maxFeeIsLoading: boolean
  tradeType: 'Buy' | 'Sell'
  total: string
  isLoading: boolean
}) => {
  return (
    <VStack w="100%" p={isShowOrderVolumeError ? '0px 0px 8px' : '8px 0px'} gap="12px">
      <HTextLabelBox
        label={currentOrderType == 'Limit' ? 'Total' : 'Est.Total'}
        value={
          total === '--' ? (
            <Text color="text_caption">--</Text>
          ) : (
            <HStack whiteSpace="nowrap" w="100%">
              <Text color="text_caption">
                {formatNumber(total)} {currentDeepBookPool?.quoteAssets?.symbol}
              </Text>
              <Text>${formatNumberWithKMB(estTotalUsd)}</Text>
            </HStack>
          )
        }
        labelStyle={{ fontSize: '14px' }}
        valueStyle={{ fontSize: '14px', maxW: '100%' }}
        wrapStyle={{ h: '20px', lineHeight: '20px' }}
      />

      <HTextLabelBox
        label="Available"
        value={
          isLoading ? (
            <Skeleton />
          ) : (
            `${formatNumber(available, undefined, undefined, Decimal.ROUND_DOWN)} ${tradeType === 'Buy' ? currentDeepBookPool?.quoteAssets?.symbol : currentDeepBookPool?.baseAssets?.symbol}`
          )
        }
        labelStyle={{ fontSize: '14px' }}
        valueStyle={{ fontSize: '14px' }}
        wrapStyle={{ h: '20px', lineHeight: '20px' }}
      />
      <HTextLabelBox
        label={
          <HStack gap="2px">
            <Text>Fee</Text>
            <Icon xlinkHref="#icon-icon_tips" svgW="20px" svgH="20px" />
          </HStack>
        }
        labelStyle={{
          textDecoration: 'unset'
        }}
        value={
          maxFeeIsLoading || isLoading ? (
            <Skeleton />
          ) : (
            `${maxFee} ${tradeType === 'Buy' ? currentDeepBookPool?.quoteAssets?.symbol : currentDeepBookPool?.baseAssets?.symbol}`
          )
        }
        valueStyle={{ fontSize: '14px' }}
        leftTooltip="Max fee estimated"
        wrapStyle={{ h: '20px', lineHeight: '20px' }}
      />
    </VStack>
  )
}

function TradeBlock() {
  const {
    tradeType,
    setTradeType,
    orderType,
    setOrderType,
    price,
    setPrice,
    amount,
    setAmount,
    currentDeepBookPool,
    isShowOrderVolumeError,
    estTotal,
    estTotalUsd,
    available,
    maxFee,
    maxFeeIsLoading,
    btnInfo,
    placeMarketOrder,
    placeOrderLoading,
    placeLimitOrder,
    total,
    tickSizeUnit,
    minSizeUnit
  } = useTradeCard()
  const { currentAccount, onWalletModal } = useAccountStore()

  const isLoading = !currentDeepBookPool?.baseAssets?.symbol || !currentDeepBookPool?.quoteAssets?.symbol

  return (
    <VStack w="100%" bg="bg_secondary" p="16px" borderRadius="12px">
      <TradeBlockTab currentTab={tradeType} setCurrentTab={setTradeType} />
      <OrderBlockTab currentTab={orderType} setCurrentTab={setOrderType} />
      <InputBlockGroup
        from={{ title: 'Price', symbol: currentDeepBookPool?.quoteAssets?.symbol, value: price, onChange: e => setPrice(e), decimals: tickSizeUnit }}
        to={{ title: 'Amount', symbol: currentDeepBookPool?.baseAssets?.symbol, value: amount, onChange: e => setAmount(e), decimals: minSizeUnit }}
        isShowOrderVolumeError={isShowOrderVolumeError}
        minSize={currentDeepBookPool?.minSize}
        isMarket={orderType == 'Market'}
      />
      <OrderInfoBlock
        currentOrderType={orderType}
        isShowOrderVolumeError={isShowOrderVolumeError}
        estTotal={estTotal}
        currentDeepBookPool={currentDeepBookPool}
        estTotalUsd={estTotalUsd}
        available={available}
        maxFee={maxFee}
        maxFeeIsLoading={maxFeeIsLoading}
        tradeType={tradeType}
        total={total}
        isLoading={isLoading}
      />
      {currentAccount?.address ? (
        <Button
          w="100%"
          fontSize="16px"
          fontWeight="500"
          isDisabled={btnInfo?.disabled}
          onClick={orderType == 'Market' ? placeMarketOrder : placeLimitOrder}
          isLoading={placeOrderLoading}
        >
          {btnInfo?.text || `Place ${tradeType} Order`}
        </Button>
      ) : (
        <Button w="100%" fontSize="16px" fontWeight="500" onClick={() => onWalletModal(true)}>
          Connect Wallet
        </Button>
      )}
    </VStack>
  )
}

export default TradeBlock
